import React, { useState } from 'react';

const GameRoles = () => {
  const [rolesData, setRolesData] = useState([
    { server: 'Tứ Linh', username: 'cosmicboy', name: 'cosboy3', class: 'VL', level: 54, status: 'Online', tags: ['PVP', 'Guild Leader'] },
    { server: 'Tứ Linh', username: 'cosmicboy', name: '★Malia™™', class: 'VL', level: 105, status: 'Online', tags: ['PVP', 'High Level', 'VIP'] },
    { server: 'Tứ Linh', username: 'hoangbm', name: 'SkyLord', class: 'TIT', level: 104, status: 'Offline', tags: ['Tank', 'Guild Member'] },
    { server: 'Tứ Linh', username: 'deity1', name: 'VM_Ga', class: 'VM', level: 45, status: 'Signing', tags: ['Mage', 'New Player'] },
    { server: 'Tứ Linh', username: 'deity1', name: 'eventShop', class: 'VL', level: 4, status: 'Offline', tags: ['Shop', 'Event'] },
    { server: 'Tứ Linh', username: 'deity1', name: '★Sun™™', class: 'TIT', level: 105, status: 'Online', tags: ['PVP', 'High Level', 'VIP'] },
    { server: 'Tứ Linh', username: 'bachdett', name: 'ConCopCon', class: 'TT', level: 103, status: 'Online', tags: ['DPS', 'Guild Member'] },
    { server: 'Tứ Linh', username: 'bachdeps', name: 'Legion', class: 'PS', level: 85, status: 'Offline', tags: ['Support', 'Mid Level'] },
    { server: 'Tứ Linh', username: 'bachdeps', name: '★Tom™™', class: 'PS', level: 103, status: 'Online', tags: ['Support', 'High Level', 'VIP'] },
    { server: 'Tứ Linh', username: 'bachdekk', name: 'DiodeZkk1', class: 'KK', level: 75, status: 'Signing', tags: ['Tank', 'Mid Level'] },
    { server: 'Tứ Linh', username: 'bachdekk', name: '★Swim™™', class: 'KK', level: 101, status: 'Online', tags: ['Tank', 'High Level', 'PVP'] },
    { server: 'Tứ Linh', username: 'bachdekk1', name: 'DiodeZ', class: 'KK', level: 105, status: 'Online', tags: ['Tank', 'High Level', 'VIP', 'Guild Leader'] },
    { server: 'Tứ Linh', username: 'bachdekk1', name: '••••', class: 'KK', level: 2, status: 'Offline', tags: ['New Player', 'Low Level'] },
    { server: 'Tứ Linh', username: 'bachdevm1', name: 'DiodeZwm', class: 'VM', level: 85, status: 'Offline', tags: ['Mage', 'Mid Level'] },
    { server: 'Tứ Linh', username: 'bachdevm1', name: '★★★Hoàng', class: 'VM', level: 105, status: 'Online', tags: ['Mage', 'High Level', 'VIP', 'PVP'] },
    { server: 'Tứ Linh', username: 'bachdevl1', name: 'ĐiệpĐiệp', class: 'VL', level: 105, status: 'Online', tags: ['DPS', 'High Level', 'VIP', 'Guild Member'] },
    { server: 'Tứ Linh', username: 'bachdevl1', name: 'Tiểu Thảo', class: 'VL', level: 1, status: 'Offline', tags: ['New Player', 'Low Level'] },
    { server: 'Tứ Linh', username: 'tinhlinhtit', name: 'Shark', class: 'TIT', level: 101, status: 'Signing', tags: ['Tank', 'High Level', 'PVP'] },
    { server: 'Tứ Linh', username: 'bmhoang', name: '★★★Ngân', class: 'TK', level: 105, status: 'Online', tags: ['DPS', 'High Level', 'VIP', 'Guild Leader'] },
    { server: 'Tứ Linh', username: 'bmhoang', name: 'ToC', class: 'ML', level: 100, status: 'Offline', tags: ['Support', 'High Level', 'Guild Member'] }
  ]);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingItem, setEditingItem] = useState(null);
  const [formData, setFormData] = useState({ server: '', username: '', name: '', class: '', level: '', status: 'Offline', tags: [] });
  const [deleteConfirm, setDeleteConfirm] = useState(null);
  const [filters, setFilters] = useState({
    server: '',
    status: '',
    tags: []
  });
  const [pagination, setPagination] = useState({
    currentPage: 1,
    itemsPerPage: 10
  });
  const [sorting, setSorting] = useState({
    column: null,
    direction: 'asc' // 'asc' or 'desc'
  });
  const [isColumnModalOpen, setIsColumnModalOpen] = useState(false);
  const [isFiltersModalOpen, setIsFiltersModalOpen] = useState(false);
  const [columnVisibility, setColumnVisibility] = useState(() => {
    // Load column visibility from localStorage or use defaults
    const saved = localStorage.getItem('gameRolesColumnVisibility');
    if (saved) {
      try {
        return JSON.parse(saved);
      } catch (e) {
        console.error('Error parsing column visibility from localStorage:', e);
      }
    }
    // Default: all columns visible (actions always visible)
    return {
      server: true,
      username: true,
      name: true,
      class: true,
      level: true,
      status: true,
      tags: true
    };
  });

  const handleAddNew = () => {
    setEditingItem(null);
    setFormData({ server: '', username: '', name: '', class: '', level: '', status: 'Offline', tags: [] });
    setIsModalOpen(true);
  };

  const handleEdit = (item, index) => {
    setEditingItem({ ...item, index });
    setFormData({ ...item });
    setIsModalOpen(true);
  };

  const handleDelete = (index) => {
    setDeleteConfirm(index);
  };

  const confirmDelete = () => {
    const newData = rolesData.filter((_, index) => index !== deleteConfirm);
    setRolesData(newData);
    setDeleteConfirm(null);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (editingItem) {
      // Update existing item
      const newData = [...rolesData];
      newData[editingItem.index] = { ...formData };
      setRolesData(newData);
    } else {
      // Add new item
      setRolesData([...rolesData, { ...formData }]);
    }
    setIsModalOpen(false);
    setFormData({ server: '', username: '', name: '', class: '', level: '', status: 'Offline', tags: [] });
  };

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const getClassColor = (className) => {
    const colors = {
      'VL': 'text-blue-600 bg-blue-100',
      'TIT': 'text-red-600 bg-red-100',
      'VM': 'text-green-600 bg-green-100',
      'TT': 'text-purple-600 bg-purple-100',
      'PS': 'text-yellow-600 bg-yellow-100',
      'KK': 'text-orange-600 bg-orange-100',
      'TK': 'text-pink-600 bg-pink-100',
      'ML': 'text-indigo-600 bg-indigo-100'
    };
    return colors[className] || 'text-gray-600 bg-gray-100';
  };

  const getStatusColor = (status) => {
    const colors = {
      'Online': 'text-green-600 bg-green-100',
      'Offline': 'text-gray-600 bg-gray-100',
      'Signing': 'text-yellow-600 bg-yellow-100'
    };
    return colors[status] || 'text-gray-600 bg-gray-100';
  };

  const getTagColor = (tag) => {
    const colors = {
      'PVP': 'text-red-600 bg-red-100',
      'Guild Leader': 'text-purple-600 bg-purple-100',
      'High Level': 'text-blue-600 bg-blue-100',
      'VIP': 'text-yellow-600 bg-yellow-100',
      'Tank': 'text-orange-600 bg-orange-100',
      'Guild Member': 'text-indigo-600 bg-indigo-100',
      'Mage': 'text-green-600 bg-green-100',
      'New Player': 'text-pink-600 bg-pink-100',
      'Shop': 'text-cyan-600 bg-cyan-100',
      'Event': 'text-emerald-600 bg-emerald-100',
      'DPS': 'text-red-600 bg-red-100',
      'Support': 'text-teal-600 bg-teal-100',
      'Mid Level': 'text-amber-600 bg-amber-100',
      'Low Level': 'text-slate-600 bg-slate-100'
    };
    return colors[tag] || 'text-gray-600 bg-gray-100';
  };

  const getSortIcon = (column) => {
    if (sorting.column !== column) {
      return (
        <svg className="w-4 h-4 text-gray-400" fill="currentColor" viewBox="0 0 20 20">
          <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd" />
        </svg>
      );
    }
    
    if (sorting.direction === 'asc') {
      return (
        <svg className="w-4 h-4 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
          <path fillRule="evenodd" d="M14.707 12.707a1 1 0 01-1.414 0L10 9.414l-3.293 3.293a1 1 0 01-1.414-1.414l4-4a1 1 0 011.414 0l4 4a1 1 0 010 1.414z" clipRule="evenodd" />
        </svg>
      );
    } else {
      return (
        <svg className="w-4 h-4 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
          <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd" />
        </svg>
      );
    }
  };

  const handleTagInput = (e) => {
    if (e.key === 'Enter' || e.key === ',') {
      e.preventDefault();
      const newTag = e.target.value.trim();
      if (newTag && !formData.tags.includes(newTag)) {
        setFormData({
          ...formData,
          tags: [...formData.tags, newTag]
        });
        e.target.value = '';
      }
    }
  };

  const removeTag = (tagToRemove) => {
    setFormData({
      ...formData,
      tags: formData.tags.filter(tag => tag !== tagToRemove)
    });
  };

  // Action functions for Actions column
  const handleAddTag = (role, index) => {
    const newTag = prompt('Enter tag to add:');
    if (newTag && newTag.trim()) {
      const newData = [...rolesData];
      if (!newData[index].tags.includes(newTag.trim())) {
        newData[index].tags.push(newTag.trim());
        setRolesData(newData);
      } else {
        alert('Tag already exists!');
      }
    }
  };

  const handleRemoveTag = (role, index) => {
    if (role.tags.length === 0) {
      alert('No tags to remove!');
      return;
    }
    
    const tagToRemove = prompt(`Enter tag to remove:\nAvailable tags: ${role.tags.join(', ')}`);
    if (tagToRemove && tagToRemove.trim()) {
      const newData = [...rolesData];
      newData[index].tags = newData[index].tags.filter(tag => tag !== tagToRemove.trim());
      setRolesData(newData);
    }
  };

  const handleSignin = (role, index) => {
    const newData = [...rolesData];
    newData[index].status = 'Signing';
    setRolesData(newData);
    
    // Simulate signin process - change to Online after 2 seconds
    setTimeout(() => {
      setRolesData(prevData => {
        const updatedData = [...prevData];
        updatedData[index].status = 'Online';
        return updatedData;
      });
    }, 2000);
  };

  // Get unique values for filter dropdowns
  const getUniqueServers = () => {
    return [...new Set(rolesData.map(role => role.server))];
  };

  const getUniqueStatuses = () => {
    return [...new Set(rolesData.map(role => role.status))];
  };

  const getUniqueTags = () => {
    const allTags = rolesData.flatMap(role => role.tags);
    return [...new Set(allTags)].sort();
  };

  // Filter the roles data based on current filters
  const getFilteredRoles = () => {
    return rolesData.filter(role => {
      const serverMatch = !filters.server || role.server === filters.server;
      const statusMatch = !filters.status || role.status === filters.status;
      const tagsMatch = filters.tags.length === 0 || filters.tags.every(filterTag => 
        role.tags.includes(filterTag)
      );
      
      return serverMatch && statusMatch && tagsMatch;
    });
  };

  const handleFilterChange = (filterType, value) => {
    setFilters(prev => ({
      ...prev,
      [filterType]: value
    }));
  };

  const handleTagFilterAdd = (tag) => {
    if (!filters.tags.includes(tag)) {
      setFilters(prev => ({
        ...prev,
        tags: [...prev.tags, tag]
      }));
    }
  };

  const handleTagFilterRemove = (tagToRemove) => {
    setFilters(prev => ({
      ...prev,
      tags: prev.tags.filter(tag => tag !== tagToRemove)
    }));
  };

  const clearAllFilters = () => {
    setFilters({
      server: '',
      status: '',
      tags: []
    });
  };

  // Column visibility functions
  const toggleColumn = (column) => {
    const newVisibility = {
      ...columnVisibility,
      [column]: !columnVisibility[column]
    };
    setColumnVisibility(newVisibility);
    localStorage.setItem('gameRolesColumnVisibility', JSON.stringify(newVisibility));
  };

  const resetColumns = () => {
    const defaultVisibility = {
      server: true,
      username: true,
      name: true,
      class: true,
      level: true,
      status: true,
      tags: true
    };
    setColumnVisibility(defaultVisibility);
    localStorage.setItem('gameRolesColumnVisibility', JSON.stringify(defaultVisibility));
  };

  const columnConfig = [
    { key: 'server', label: 'Server', sortable: true },
    { key: 'username', label: 'Username', sortable: true },
    { key: 'name', label: 'Name', sortable: true },
    { key: 'class', label: 'Class', sortable: false },
    { key: 'level', label: 'Level', sortable: true },
    { key: 'status', label: 'Status', sortable: true },
    { key: 'tags', label: 'Tags', sortable: false }
  ];

  // Sorting logic
  const handleSort = (column) => {
    setSorting(prev => {
      if (prev.column === column) {
        // If clicking the same column, toggle direction
        return {
          column,
          direction: prev.direction === 'asc' ? 'desc' : 'asc'
        };
      } else {
        // If clicking a different column, set to ascending
        return {
          column,
          direction: 'asc'
        };
      }
    });
  };

  const getSortedRoles = (roles) => {
    if (!sorting.column) return roles;
    
    return [...roles].sort((a, b) => {
      let aValue, bValue;
      
      switch (sorting.column) {
        case 'level':
          aValue = parseInt(a.level);
          bValue = parseInt(b.level);
          break;
        case 'name':
          aValue = a.name.toLowerCase();
          bValue = b.name.toLowerCase();
          break;
        case 'username':
          aValue = a.username.toLowerCase();
          bValue = b.username.toLowerCase();
          break;
        case 'server':
          aValue = a.server.toLowerCase();
          bValue = b.server.toLowerCase();
          break;
        case 'status':
          aValue = a.status.toLowerCase();
          bValue = b.status.toLowerCase();
          break;
        default:
          return 0;
      }
      
      if (aValue < bValue) {
        return sorting.direction === 'asc' ? -1 : 1;
      }
      if (aValue > bValue) {
        return sorting.direction === 'asc' ? 1 : -1;
      }
      return 0;
    });
  };

  // Pagination logic
  const filteredRoles = getFilteredRoles();
  const sortedRoles = getSortedRoles(filteredRoles);
  const totalItems = sortedRoles.length;
  const totalPages = Math.ceil(totalItems / pagination.itemsPerPage);
  const startIndex = (pagination.currentPage - 1) * pagination.itemsPerPage;
  const endIndex = startIndex + pagination.itemsPerPage;
  const paginatedRoles = sortedRoles.slice(startIndex, endIndex);

  const handlePageChange = (page) => {
    setPagination(prev => ({
      ...prev,
      currentPage: page
    }));
  };

  const handleItemsPerPageChange = (itemsPerPage) => {
    setPagination({
      currentPage: 1,
      itemsPerPage: parseInt(itemsPerPage)
    });
  };

  const goToFirstPage = () => handlePageChange(1);
  const goToPreviousPage = () => handlePageChange(Math.max(1, pagination.currentPage - 1));
  const goToNextPage = () => handlePageChange(Math.min(totalPages, pagination.currentPage + 1));
  const goToLastPage = () => handlePageChange(totalPages);

  // Reset to first page when filters change
  React.useEffect(() => {
    setPagination(prev => ({
      ...prev,
      currentPage: 1
    }));
  }, [filters]);

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white shadow rounded-lg">
          <div className="px-4 py-5 sm:p-6">
            <div className="flex justify-between items-center mb-8">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Perfect World - Game Roles</h1>
                <p className="text-gray-600 mt-2">
                  Character roles and information for Perfect World game.
                </p>
              </div>
              <div className="flex space-x-3">
                <button
                  onClick={() => setIsColumnModalOpen(true)}
                  className="bg-gray-600 hover:bg-gray-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                >
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M3 4a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zm0 4a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zm0 4a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zm0 4a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z" clipRule="evenodd" />
                  </svg>
                  <span>Columns</span>
                </button>
                <button
                  onClick={handleAddNew}
                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                >
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                  </svg>
                  <span>Add New Role</span>
                </button>
              </div>
            </div>

            {/* Filters Button */}
            <div className="mb-6">
              <button
                onClick={() => setIsFiltersModalOpen(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M3 3a1 1 0 011-1h12a1 1 0 011 1v3a1 1 0 01-.293.707L12 11.414V15a1 1 0 01-.293.707l-2 2A1 1 0 018 17v-5.586L3.293 6.707A1 1 0 013 6V3z" clipRule="evenodd" />
                </svg>
                Filters
                {(filters.server || filters.status || filters.tags.length > 0) && (
                  <span className="bg-red-500 text-white text-xs rounded-full px-2 py-0.5">
                    {[filters.server, filters.status, ...filters.tags].filter(Boolean).length}
                  </span>
                )}
              </button>
              
              {/* Filter Results Summary */}
              <div className="mt-2 text-sm text-gray-600">
                Showing {startIndex + 1}-{Math.min(endIndex, totalItems)} of {totalItems} roles
                {filters.server && ` • Server: ${filters.server}`}
                {filters.status && ` • Status: ${filters.status}`}
                {filters.tags.length > 0 && ` • Tags: ${filters.tags.join(', ')}`}
              </div>
            </div>
            
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    {columnConfig.map((column) => {
                      if (!columnVisibility[column.key]) return null;
                      
                      const isSortable = column.sortable;
                      const headerClass = isSortable 
                        ? "px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer hover:bg-gray-100 select-none"
                        : "px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider";
                      
                      return (
                        <th
                          key={column.key}
                          className={headerClass}
                          onClick={isSortable ? () => handleSort(column.key) : undefined}
                        >
                          <div className="flex items-center space-x-1">
                            <span>{column.label}</span>
                            {isSortable && getSortIcon(column.key)}
                          </div>
                        </th>
                      );
                    })}
                    {/* Actions column - always visible */}
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {paginatedRoles.map((role, index) => {
                    const originalIndex = rolesData.findIndex(r => r.name === role.name && r.username === role.username);
                    return (
                    <tr key={`${role.name}-${originalIndex}`} className={index % 2 === 0 ? 'bg-white' : 'bg-pink-50'}>
                      {columnVisibility.server && (
                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                          {role.server}
                        </td>
                      )}
                      {columnVisibility.username && (
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          {role.username}
                        </td>
                      )}
                      {columnVisibility.name && (
                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                          {role.name}
                        </td>
                      )}
                      {columnVisibility.class && (
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getClassColor(role.class)}`}>
                            {role.class}
                          </span>
                        </td>
                      )}
                      {columnVisibility.level && (
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 font-mono">
                          {role.level}
                        </td>
                      )}
                      {columnVisibility.status && (
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusColor(role.status)}`}>
                            {role.status}
                          </span>
                        </td>
                      )}
                      {columnVisibility.tags && (
                        <td className="px-6 py-4 text-sm text-gray-500">
                          <div className="flex flex-wrap gap-1">
                            {role.tags.map((tag, tagIndex) => (
                              <span
                                key={tagIndex}
                                className={`inline-flex px-2 py-1 text-xs font-medium rounded-full ${getTagColor(tag)}`}
                              >
                                {tag}
                              </span>
                            ))}
                          </div>
                        </td>
                      )}
                      {/* Actions column - always visible */}
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <div className="flex flex-wrap gap-1">
                          <button
                            onClick={() => handleAddTag(role, originalIndex)}
                            className="text-green-600 hover:text-green-900 transition-colors p-1"
                            title="Add tag"
                          >
                            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                            </svg>
                          </button>
                          <button
                            onClick={() => handleRemoveTag(role, originalIndex)}
                            className="text-orange-600 hover:text-orange-900 transition-colors p-1"
                            title="Remove tag"
                          >
                            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                            </svg>
                          </button>
                          <button
                            onClick={() => handleSignin(role, originalIndex)}
                            className="text-purple-600 hover:text-purple-900 transition-colors p-1"
                            title="Sign in"
                            disabled={role.status === 'Signing'}
                          >
                            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M3 3a1 1 0 000 2v8a2 2 0 002 2h2.586l-1.293 1.293a1 1 0 101.414 1.414L10 15.414l2.293 2.293a1 1 0 001.414-1.414L12.414 15H15a2 2 0 002-2V5a1 1 0 100-2H3zm11.707 4.707a1 1 0 00-1.414-1.414L10 9.586 8.707 8.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                            </svg>
                          </button>
                        </div>
                      </td>
                    </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>

            {/* Pagination */}
            {totalPages > 1 && (
              <div className="mt-6 flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
                <div className="flex flex-1 justify-between sm:hidden">
                  <button
                    onClick={goToPreviousPage}
                    disabled={pagination.currentPage === 1}
                    className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Previous
                  </button>
                  <button
                    onClick={goToNextPage}
                    disabled={pagination.currentPage === totalPages}
                    className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Next
                  </button>
                </div>
                <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
                  <div className="flex items-center space-x-4">
                    <div className="flex items-center space-x-2">
                      <label className="text-sm font-medium text-gray-700">Show:</label>
                      <select
                        value={pagination.itemsPerPage}
                        onChange={(e) => handleItemsPerPageChange(e.target.value)}
                        className="rounded-md border-gray-300 text-sm focus:border-blue-500 focus:ring-blue-500"
                      >
                        <option value={10}>10</option>
                        <option value={25}>25</option>
                        <option value={50}>50</option>
                        <option value={100}>100</option>
                      </select>
                      <span className="text-sm text-gray-700">per page</span>
                    </div>
                    <div className="text-sm text-gray-700">
                      Page <span className="font-medium">{pagination.currentPage}</span> of{' '}
                      <span className="font-medium">{totalPages}</span>
                    </div>
                  </div>
                  <div>
                    <nav className="isolate inline-flex -space-x-px rounded-md shadow-sm" aria-label="Pagination">
                      <button
                        onClick={goToFirstPage}
                        disabled={pagination.currentPage === 1}
                        className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <span className="sr-only">First</span>
                        <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                          <path fillRule="evenodd" d="M15.79 14.77a.75.75 0 01-1.06.02l-4.5-4.25a.75.75 0 010-1.08l4.5-4.25a.75.75 0 111.04 1.08L11.832 10l3.938 3.71a.75.75 0 01.02 1.06zm-6 0a.75.75 0 01-1.06.02l-4.5-4.25a.75.75 0 010-1.08l4.5-4.25a.75.75 0 111.04 1.08L5.832 10l3.938 3.71a.75.75 0 01.02 1.06z" clipRule="evenodd" />
                        </svg>
                      </button>
                      <button
                        onClick={goToPreviousPage}
                        disabled={pagination.currentPage === 1}
                        className="relative inline-flex items-center px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <span className="sr-only">Previous</span>
                        <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                          <path fillRule="evenodd" d="M12.79 5.23a.75.75 0 01-.02 1.06L8.832 10l3.938 3.71a.75.75 0 01-1.04 1.08l-4.5-4.25a.75.75 0 010-1.08l4.5-4.25a.75.75 0 011.06.02z" clipRule="evenodd" />
                        </svg>
                      </button>
                      
                      {/* Page Numbers */}
                      {(() => {
                        const pages = [];
                        const maxVisiblePages = 5;
                        let startPage = Math.max(1, pagination.currentPage - Math.floor(maxVisiblePages / 2));
                        let endPage = Math.min(totalPages, startPage + maxVisiblePages - 1);
                        
                        if (endPage - startPage + 1 < maxVisiblePages) {
                          startPage = Math.max(1, endPage - maxVisiblePages + 1);
                        }
                        
                        for (let i = startPage; i <= endPage; i++) {
                          pages.push(
                            <button
                              key={i}
                              onClick={() => handlePageChange(i)}
                              className={`relative inline-flex items-center px-4 py-2 text-sm font-semibold ${
                                i === pagination.currentPage
                                  ? 'z-10 bg-blue-600 text-white focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-blue-600'
                                  : 'text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0'
                              }`}
                            >
                              {i}
                            </button>
                          );
                        }
                        return pages;
                      })()}
                      
                      <button
                        onClick={goToNextPage}
                        disabled={pagination.currentPage === totalPages}
                        className="relative inline-flex items-center px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <span className="sr-only">Next</span>
                        <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                          <path fillRule="evenodd" d="M7.21 14.77a.75.75 0 01.02-1.06L11.168 10 7.23 6.29a.75.75 0 111.04-1.08l4.5 4.25a.75.75 0 010 1.08l-4.5 4.25a.75.75 0 01-1.06-.02z" clipRule="evenodd" />
                        </svg>
                      </button>
                      <button
                        onClick={goToLastPage}
                        disabled={pagination.currentPage === totalPages}
                        className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0 disabled:opacity-50 disabled:cursor-not-allowed"
                      >
                        <span className="sr-only">Last</span>
                        <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                          <path fillRule="evenodd" d="M4.21 5.23a.75.75 0 01.02-1.06L8.168 10 4.23 6.29a.75.75 0 111.04-1.08l4.5 4.25a.75.75 0 010 1.08l-4.5 4.25a.75.75 0 01-1.06-.02z" clipRule="evenodd" />
                        </svg>
                      </button>
                    </nav>
                  </div>
                </div>
              </div>
            )}
            
            <div className="mt-6 p-4 bg-blue-50 rounded-md">
              <h3 className="text-sm font-medium text-blue-800 mb-2">Class Legend:</h3>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-2 text-sm text-blue-700">
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-blue-600 bg-blue-100">VL</span>
                  <span>Venomancer</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-red-600 bg-red-100">TIT</span>
                  <span>Titan</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-green-600 bg-green-100">VM</span>
                  <span>Viper Mage</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-purple-600 bg-purple-100">TT</span>
                  <span>Thunder Tiger</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-yellow-600 bg-yellow-100">PS</span>
                  <span>Psychic</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-orange-600 bg-orange-100">KK</span>
                  <span>Knight</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-pink-600 bg-pink-100">TK</span>
                  <span>Thunder Knight</span>
                </div>
                <div className="flex items-center space-x-2">
                  <span className="inline-flex px-2 py-1 text-xs font-semibold rounded-full text-indigo-600 bg-indigo-100">ML</span>
                  <span>Mystic Lord</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Column Visibility Modal */}
      {isColumnModalOpen && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-medium text-gray-900">Column Visibility</h3>
                <button
                  onClick={() => setIsColumnModalOpen(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              
              <div className="space-y-3 mb-6">
                {columnConfig.map((column) => (
                  <label
                    key={column.key}
                    className="flex items-center space-x-3 cursor-pointer hover:bg-gray-50 p-3 rounded-md transition-colors"
                  >
                    <input
                      type="checkbox"
                      checked={columnVisibility[column.key]}
                      onChange={() => toggleColumn(column.key)}
                      className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                    />
                    <div className="flex-1">
                      <span className="text-sm font-medium text-gray-700">
                        {column.label}
                      </span>
                      {column.sortable && (
                        <span className="ml-2 text-xs text-gray-500">(sortable)</span>
                      )}
                    </div>
                  </label>
                ))}
              </div>
              
              <div className="flex justify-between">
                <button
                  onClick={resetColumns}
                  className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                >
                  Reset to Default
                </button>
                <button
                  onClick={() => setIsColumnModalOpen(false)}
                  className="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
                >
                  Done
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Filters Modal */}
      {isFiltersModalOpen && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-medium text-gray-900">Filters</h3>
                <button
                  onClick={() => setIsFiltersModalOpen(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              
              <div className="space-y-4 mb-6">
                {/* Server Filter */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Server
                  </label>
                  <select
                    value={filters.server}
                    onChange={(e) => handleFilterChange('server', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="">All Servers</option>
                    {getUniqueServers().map(server => (
                      <option key={server} value={server}>{server}</option>
                    ))}
                  </select>
                </div>

                {/* Status Filter */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Status
                  </label>
                  <select
                    value={filters.status}
                    onChange={(e) => handleFilterChange('status', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option value="">All Status</option>
                    {getUniqueStatuses().map(status => (
                      <option key={status} value={status}>{status}</option>
                    ))}
                  </select>
                </div>

                {/* Tags Filter */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Tags
                  </label>
                  <div className="border border-gray-300 rounded-md p-2 min-h-[40px] focus-within:ring-2 focus-within:ring-blue-500">
                    <div className="flex flex-wrap gap-1 mb-2">
                      {filters.tags.map((tag, index) => (
                        <span
                          key={index}
                          className="inline-flex items-center px-2 py-1 text-xs font-medium rounded-full bg-blue-100 text-blue-800"
                        >
                          {tag}
                          <button
                            type="button"
                            onClick={() => handleTagFilterRemove(tag)}
                            className="ml-1 text-blue-600 hover:text-blue-800"
                          >
                            <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                            </svg>
                          </button>
                        </span>
                      ))}
                    </div>
                    <select
                      value=""
                      onChange={(e) => {
                        if (e.target.value) {
                          handleTagFilterAdd(e.target.value);
                          e.target.value = '';
                        }
                      }}
                      className="w-full px-2 py-1 border-0 focus:outline-none text-sm"
                    >
                      <option value="">Select tags to filter...</option>
                      {getUniqueTags().map(tag => (
                        <option key={tag} value={tag}>{tag}</option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>
              
              <div className="flex justify-between">
                <button
                  onClick={clearAllFilters}
                  className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                >
                  Clear All Filters
                </button>
                <button
                  onClick={() => setIsFiltersModalOpen(false)}
                  className="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
                >
                  Apply Filters
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Add/Edit Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                {editingItem ? 'Edit Role' : 'Add New Role'}
              </h3>
              <form onSubmit={handleSubmit}>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Server
                  </label>
                  <input
                    type="text"
                    name="server"
                    value={formData.server}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Username
                  </label>
                  <input
                    type="text"
                    name="username"
                    value={formData.username}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Character Name
                  </label>
                  <input
                    type="text"
                    name="name"
                    value={formData.name}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Class
                  </label>
                  <select
                    name="class"
                    value={formData.class}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  >
                    <option value="">Select Class</option>
                    <option value="VL">VL - Venomancer</option>
                    <option value="TIT">TIT - Titan</option>
                    <option value="VM">VM - Viper Mage</option>
                    <option value="TT">TT - Thunder Tiger</option>
                    <option value="PS">PS - Psychic</option>
                    <option value="KK">KK - Knight</option>
                    <option value="TK">TK - Thunder Knight</option>
                    <option value="ML">ML - Mystic Lord</option>
                  </select>
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Level
                  </label>
                  <input
                    type="number"
                    name="level"
                    value={formData.level}
                    onChange={handleInputChange}
                    min="1"
                    max="105"
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Status
                  </label>
                  <select
                    name="status"
                    value={formData.status}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  >
                    <option value="Online">Online</option>
                    <option value="Offline">Offline</option>
                    <option value="Signing">Signing</option>
                  </select>
                </div>
                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Tags
                  </label>
                  <div className="border border-gray-300 rounded-md p-2 min-h-[40px] focus-within:ring-2 focus-within:ring-blue-500">
                    <div className="flex flex-wrap gap-1 mb-2">
                      {formData.tags.map((tag, index) => (
                        <span
                          key={index}
                          className="inline-flex items-center px-2 py-1 text-xs font-medium rounded-full bg-blue-100 text-blue-800"
                        >
                          {tag}
                          <button
                            type="button"
                            onClick={() => removeTag(tag)}
                            className="ml-1 text-blue-600 hover:text-blue-800"
                          >
                            <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                            </svg>
                          </button>
                        </span>
                      ))}
                    </div>
                    <input
                      type="text"
                      placeholder="Type a tag and press Enter or comma to add..."
                      onKeyDown={handleTagInput}
                      className="w-full px-2 py-1 border-0 focus:outline-none text-sm"
                    />
                  </div>
                  <p className="text-xs text-gray-500 mt-1">
                    Press Enter or comma to add tags. Click the X to remove tags.
                  </p>
                </div>
                <div className="flex justify-end space-x-3">
                  <button
                    type="button"
                    onClick={() => setIsModalOpen(false)}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
                  >
                    {editingItem ? 'Update' : 'Add'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {deleteConfirm !== null && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-red-100 mb-4">
                <svg className="h-6 w-6 text-red-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
                </svg>
              </div>
              <h3 className="text-lg font-medium text-gray-900 text-center mb-2">
                Delete Role
              </h3>
              <p className="text-sm text-gray-500 text-center mb-6">
                Are you sure you want to delete <strong>{rolesData[deleteConfirm]?.name}</strong>? This action cannot be undone.
              </p>
              <div className="flex justify-center space-x-3">
                <button
                  onClick={() => setDeleteConfirm(null)}
                  className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={confirmDelete}
                  className="px-4 py-2 text-sm font-medium text-white bg-red-600 hover:bg-red-700 rounded-md transition-colors"
                >
                  Delete
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GameRoles;
