import React, { useState, useEffect } from 'react';
import apiService from '../services/apiService';

const GameOffsets = () => {
  const [offsetsData, setOffsetsData] = useState([]);
  const [versions, setVersions] = useState([]);
  const [selectedVersion, setSelectedVersion] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingItem, setEditingItem] = useState(null);
  const [formData, setFormData] = useState({ name: '', value: '', note: '', version: '' });

  // Fetch versions on component mount
  useEffect(() => {
    fetchVersions();
  }, []);

  // Fetch offsets when version changes
  useEffect(() => {
    if (selectedVersion) {
      fetchOffsets(selectedVersion);
    }
  }, [selectedVersion]);

  const fetchVersions = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await apiService.executeSavedSql('pwtools', 'select_all_offset_versions', []);
      setVersions(response.data || []);
      // Auto-select first version if available
      if (response.data && response.data.length > 0) {
        const firstVersion = response.data[0].version || response.data[0];
        setSelectedVersion(String(firstVersion));
      }
    } catch (err) {
      setError('Failed to fetch versions: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const fetchOffsets = async (version) => {
    try {
      setLoading(true);
      setError(null);
      const response = await apiService.executeSavedSql('pwtools', 'select_all_offsets_by_version', [String(version)]);
      setOffsetsData(response.data || []);
    } catch (err) {
      setError('Failed to fetch offsets: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleVersionChange = (e) => {
    setSelectedVersion(e.target.value);
  };

  const handleAddNew = () => {
    setEditingItem(null);
    setFormData({ name: '', value: '', note: '', version: selectedVersion || '' });
    setIsModalOpen(true);
  };

  const handleEdit = (item, index) => {
    setEditingItem({ ...item, index });
    setFormData({ name: item.name, value: item.value, note: item.note, version: String(item.version) });
    setIsModalOpen(true);
  };


  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      setError(null);
      
      if (editingItem) {
        // Update existing item using backend API
        const params = [
          formData.name,
          formData.value,
          formData.note,
          formData.version,
          String(editingItem.id)
        ];
        
        const updateResponse = await apiService.executeSavedSql('pwtools', 'update_pw_offset', params);
        
        // Check if update was successful and affected exactly 1 row
        if (updateResponse.rows_affected === 1) {
          // Check if the updated version is in the current versions list
          const versionExists = versions.some(v => 
            String(typeof v === 'object' ? v.version : v) === String(formData.version)
          );
          
          // If version doesn't exist in the list, refresh the versions list
          if (!versionExists) {
            await fetchVersions();
          }
        }
        
        // Refresh the offsets data after successful update
        await fetchOffsets(selectedVersion);
      } else {
        // Add new item using backend API
        const params = [
          formData.name,
          formData.value,
          formData.note,
          formData.version
        ];
        
        await apiService.executeSavedSql('pwtools', 'add_new_pw_offset', params);
        
        // Check if the new version is in the current versions list
        const versionExists = versions.some(v => 
          String(typeof v === 'object' ? v.version : v) === String(formData.version)
        );
        
        // If version doesn't exist in the list, refresh the versions list
        if (!versionExists) {
          await fetchVersions();
        }
        
        // Refresh the offsets data after successful addition
        await fetchOffsets(selectedVersion);
      }
      
      setIsModalOpen(false);
      setFormData({ name: '', value: '', note: '', version: '' });
    } catch (err) {
      setError('Failed to save offset: ' + err.message);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white shadow rounded-lg">
          <div className="px-4 py-5 sm:p-6">
            <div className="flex justify-between items-center mb-8">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Perfect World - Game Offsets</h1>
                <p className="text-gray-600 mt-2">
                  This table contains game offsets and memory addresses for Perfect World game modifications.
                </p>
              </div>
              <div className="flex items-center space-x-4">
                <div className="flex items-center space-x-2">
                  <label htmlFor="version-select" className="text-sm font-medium text-gray-700">
                    Version:
                  </label>
                  <select
                    id="version-select"
                    value={selectedVersion}
                    onChange={handleVersionChange}
                    className="px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    disabled={loading}
                  >
                    <option value="">Select Version</option>
                    {versions.map((version, index) => (
                      <option key={index} value={typeof version === 'object' ? version.version : version}>
                        {typeof version === 'object' ? version.version : version}
                      </option>
                    ))}
                  </select>
                </div>
                <button
                  onClick={handleAddNew}
                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                  disabled={loading}
                >
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                  </svg>
                  <span>Add New Item</span>
                </button>
              </div>
            </div>

            {/* Error Message */}
            {error && (
              <div className="mb-4 p-4 bg-red-50 border border-red-200 rounded-md">
                <div className="flex">
                  <div className="flex-shrink-0">
                    <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                    </svg>
                  </div>
                  <div className="ml-3">
                    <h3 className="text-sm font-medium text-red-800">Error</h3>
                    <div className="mt-2 text-sm text-red-700">
                      <p>{error}</p>
                    </div>
                  </div>
                </div>
              </div>
            )}

            {/* Loading State */}
            {loading && (
              <div className="mb-4 p-4 bg-blue-50 border border-blue-200 rounded-md">
                <div className="flex items-center">
                  <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-blue-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  <span className="text-sm text-blue-700">Loading...</span>
                </div>
              </div>
            )}
            
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      ID
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Name
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Value
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Note
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Version
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {offsetsData.length === 0 && !loading ? (
                    <tr>
                      <td colSpan="6" className="px-6 py-8 text-center text-sm text-gray-500">
                        {selectedVersion ? 'No offsets found for the selected version.' : 'Please select a version to view offsets.'}
                      </td>
                    </tr>
                  ) : (
                    offsetsData.map((offset, index) => (
                    <tr key={`${offset.id}-${index}`} className={index % 2 === 0 ? 'bg-white' : 'bg-pink-50'}>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {offset.id}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {offset.name}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 font-mono">
                        {offset.value}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-500">
                        {offset.note}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                        {offset.version}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                        <button
                          onClick={() => handleEdit(offset, index)}
                          className="text-blue-600 hover:text-blue-900 transition-colors"
                          title="Edit item"
                        >
                          <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                            <path d="M13.586 3.586a2 2 0 112.828 2.828l-.793.793-2.828-2.828.793-.793zM11.379 5.793L3 14.172V17h2.828l8.38-8.379-2.83-2.828z" />
                          </svg>
                        </button>
                      </td>
                    </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
            
            <div className="mt-6 p-4 bg-blue-50 rounded-md">
              <h3 className="text-sm font-medium text-blue-800 mb-2">Legend:</h3>
              <ul className="text-sm text-blue-700 space-y-1">
                <li>• <strong>ID:</strong> Unique identifier for the offset record</li>
                <li>• <strong>Name:</strong> Offset identifier</li>
                <li>• <strong>Value:</strong> Hexadecimal or decimal memory address/offset</li>
                <li>• <strong>Note:</strong> Additional information, often including version numbers and Vietnamese descriptions</li>
                <li>• <strong>Version:</strong> Game version this offset applies to</li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      {/* Add/Edit Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                {editingItem ? 'Edit Offset' : 'Add New Offset'}
              </h3>
              <form onSubmit={handleSubmit}>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Name
                  </label>
                  <input
                    type="text"
                    name="name"
                    value={formData.name}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                    disabled={loading}
                  />
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Value
                  </label>
                  <input
                    type="text"
                    name="value"
                    value={formData.value}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 font-mono"
                    required
                    disabled={loading}
                  />
                </div>
                <div className="mb-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Note
                  </label>
                  <textarea
                    name="note"
                    value={formData.note}
                    onChange={handleInputChange}
                    rows={3}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    disabled={loading}
                  />
                </div>
                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Version
                  </label>
                  <input
                    type="number"
                    name="version"
                    value={formData.version}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                    disabled={loading}
                    min="1"
                    step="1"
                  />
                  <p className="text-xs text-gray-500 mt-1">
                    Enter the game version number (e.g., 502, 416, etc.)
                  </p>
                </div>
                <div className="flex justify-end space-x-3">
                  <button
                    type="button"
                    onClick={() => setIsModalOpen(false)}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                    disabled={loading}
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                    disabled={loading}
                  >
                    {loading ? 'Saving...' : (editingItem ? 'Update' : 'Add')}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}

    </div>
  );
};

export default GameOffsets;
