import React, { useState, useEffect } from 'react';

const PWAInstallPrompt = () => {
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [showInstallButton, setShowInstallButton] = useState(false);
  const [isInstalled, setIsInstalled] = useState(false);
  const [showManualInstall, setShowManualInstall] = useState(false);

  useEffect(() => {
    // Check if the app is already installed
    const checkIfInstalled = () => {
      const isStandalone = window.matchMedia('(display-mode: standalone)').matches;
      const isInAppBrowser = window.navigator.standalone === true;
      const isInstalled = isStandalone || isInAppBrowser;
      setIsInstalled(isInstalled);
      return isInstalled;
    };

    // Check installation status on load
    if (checkIfInstalled()) {
      setShowInstallButton(false);
      return;
    }

    // Listen for the beforeinstallprompt event
    const handleBeforeInstallPrompt = (e) => {
      console.log('beforeinstallprompt event fired', e);
      // Prevent the mini-infobar from appearing on mobile
      e.preventDefault();
      // Stash the event so it can be triggered later
      setDeferredPrompt(e);
      // Show the install button
      setShowInstallButton(true);
    };

    // Debug: Log PWA criteria
    const checkPWACriteria = () => {
      console.log('PWA Debug Info:');
      console.log('- HTTPS:', window.location.protocol === 'https:');
      console.log('- Service Worker:', 'serviceWorker' in navigator);
      console.log('- Manifest:', document.querySelector('link[rel="manifest"]')?.href);
      console.log('- Display mode:', window.matchMedia('(display-mode: standalone)').matches);
      console.log('- User agent:', navigator.userAgent);
    };

    // Run debug check
    checkPWACriteria();

    // Listen for the appinstalled event
    const handleAppInstalled = () => {
      console.log('PWA was installed');
      // Hide the install button
      setShowInstallButton(false);
      setIsInstalled(true);
      // Clear the deferredPrompt
      setDeferredPrompt(null);
    };

    // Check if user has already dismissed the install prompt
    const hasUserDismissed = localStorage.getItem('pwa-install-dismissed');
    if (hasUserDismissed) {
      setShowInstallButton(false);
    }

    // Set a timeout to show manual install option if no automatic prompt appears
    const manualInstallTimeout = setTimeout(() => {
      if (!showInstallButton && !isInstalled && !hasUserDismissed) {
        setShowManualInstall(true);
      }
    }, 10000); // Show after 10 seconds

    // Add event listeners
    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    window.addEventListener('appinstalled', handleAppInstalled);

    // Re-check installation status periodically
    const interval = setInterval(() => {
      if (checkIfInstalled()) {
        setShowInstallButton(false);
        clearInterval(interval);
      }
    }, 1000);

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      window.removeEventListener('appinstalled', handleAppInstalled);
      clearInterval(interval);
      clearTimeout(manualInstallTimeout);
    };
  }, [showInstallButton, isInstalled]);

  const handleInstallClick = async () => {
    if (!deferredPrompt) return;

    try {
      // Show the install prompt
      deferredPrompt.prompt();

      // Wait for the user to respond to the prompt
      const { outcome } = await deferredPrompt.userChoice;

      if (outcome === 'accepted') {
        console.log('User accepted the install prompt');
      } else {
        console.log('User dismissed the install prompt');
        // Store dismissal in localStorage to avoid showing again
        localStorage.setItem('pwa-install-dismissed', 'true');
      }

      // Clear the deferredPrompt
      setDeferredPrompt(null);
      setShowInstallButton(false);
    } catch (error) {
      console.error('Error showing install prompt:', error);
      setShowInstallButton(false);
    }
  };

  const handleDismiss = () => {
    setShowInstallButton(false);
    setShowManualInstall(false);
    localStorage.setItem('pwa-install-dismissed', 'true');
  };

  const handleManualInstall = () => {
    // For iOS Safari, show instructions
    if (/iPad|iPhone|iPod/.test(navigator.userAgent)) {
      alert('To install this app on your iOS device:\n1. Tap the Share button\n2. Scroll down and tap "Add to Home Screen"\n3. Tap "Add"');
    } else {
      // For other browsers, show general instructions
      alert('To install this app:\n1. Look for the install icon in your browser\'s address bar\n2. Or go to your browser menu and look for "Install" or "Add to Home Screen"');
    }
    handleDismiss();
  };

  // Don't show if already installed
  if (isInstalled) return null;

  // Show manual install option if automatic prompt didn't appear
  if (showManualInstall && !showInstallButton) {
    return (
      <div className="fixed bottom-4 right-4 z-50">
        <div className="bg-green-600 text-white rounded-lg shadow-lg p-4 max-w-sm relative">
          <button
            onClick={handleDismiss}
            className="absolute top-2 right-2 text-green-200 hover:text-white transition-colors"
            aria-label="Dismiss install prompt"
          >
            <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
          <div className="flex items-center space-x-3 pr-6">
            <div className="flex-shrink-0">
              <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 18h.01M8 21h8a2 2 0 002-2V5a2 2 0 00-2-2H8a2 2 0 00-2 2v14a2 2 0 002 2z" />
              </svg>
            </div>
            <div className="flex-1">
              <h3 className="text-sm font-medium">Install HoB Tools</h3>
              <p className="text-xs text-green-100">Get quick access to the app</p>
            </div>
            <button
              onClick={handleManualInstall}
              className="bg-white text-green-600 px-3 py-1 rounded-md text-sm font-medium hover:bg-green-50 transition-colors"
            >
              Install
            </button>
          </div>
        </div>
      </div>
    );
  }

  // Don't show automatic install if not available
  if (!showInstallButton) return null;

  return (
    <div className="fixed bottom-4 right-4 z-50">
      <div className="bg-blue-600 text-white rounded-lg shadow-lg p-4 max-w-sm relative">
        <button
          onClick={handleDismiss}
          className="absolute top-2 right-2 text-blue-200 hover:text-white transition-colors"
          aria-label="Dismiss install prompt"
        >
          <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
        <div className="flex items-center space-x-3 pr-6">
          <div className="flex-shrink-0">
            <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 18h.01M8 21h8a2 2 0 002-2V5a2 2 0 00-2-2H8a2 2 0 00-2 2v14a2 2 0 002 2z" />
            </svg>
          </div>
          <div className="flex-1">
            <h3 className="text-sm font-medium">Install HoB Tools</h3>
            <p className="text-xs text-blue-100">Add to home screen for quick access</p>
          </div>
          <button
            onClick={handleInstallClick}
            className="bg-white text-blue-600 px-3 py-1 rounded-md text-sm font-medium hover:bg-blue-50 transition-colors"
          >
            Install
          </button>
        </div>
      </div>
    </div>
  );
};

export default PWAInstallPrompt;

