import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { AuthProvider } from './contexts/AuthContext';
import ProtectedRoute from './components/ProtectedRoute';
import Header from './components/Header';
import Footer from './components/Footer';
import PWAInstallPrompt from './components/PWAInstallPrompt';
import Home from './pages/Home';
import LoginForm from './components/LoginForm';
import Dashboard from './components/Dashboard';
import Profile from './pages/Profile';
// Users page removed - user management is handled by Kanidm OIDC
import MediaPlayer from './components/MediaPlayer';
import Mines from './pages/Mines';
import Game2048 from './pages/Game2048';
import QRGenerator from './pages/QRGenerator';
import Encoding from './pages/Encoding';
import SQLStudio from './pages/SQLStudio';
import LuaStudio from './pages/LuaStudio';
import SwaggerUI from './pages/SwaggerUI';
import GameOffsets from './pages/GameOffsets';
import GameRoles from './pages/GameRoles';
import StateMachine from './pages/StateMachine';
import OidcCallback from './pages/OidcCallback';
import { useRoutePersistence } from './hooks/useRoutePersistence';
// Service worker is registered in index.js


// Simple Register page
const Register = () => (
  <div className="min-h-screen bg-gray-50 py-16">
    <div className="max-w-md mx-auto px-4 sm:px-6 lg:px-8">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-8">Create Account</h1>
        <div className="bg-white shadow rounded-lg p-8">
          <p className="text-gray-600 mb-6">
            Registration functionality would be implemented here.
          </p>
          <p className="text-sm text-gray-500">
            For demo purposes, you can use the login page with: admin@example.com / password
          </p>
        </div>
      </div>
    </div>
  </div>
);



// Component that uses the route persistence hook
const AppContent = () => {
  // Initialize route persistence
  useRoutePersistence();

  return (
    <div className="App">
      <Header />
      <main>
        <Routes>
        {/* Public routes (whitelist) */}
        <Route path="/" element={<Home />} />
        <Route path="/login" element={<LoginForm />} />
        <Route path="/register" element={<Register />} />
        <Route path="/callback" element={<OidcCallback />} />
        <Route path="/media" element={<MediaPlayer />} />
        <Route path="/games/mines" element={<Mines />} />
        <Route path="/games/2048" element={<Game2048 />} />
        <Route path="/tools/qr-generation" element={<QRGenerator />} />
        <Route path="/tools/encoding" element={<Encoding />} />
        <Route
          path="/tools/sql-studio"
          element={
            <ProtectedRoute requireAdmin={true}>
              <SQLStudio />
            </ProtectedRoute>
          }
        />
        <Route
          path="/tools/lua-studio"
          element={
            <ProtectedRoute requireAdmin={true}>
              <LuaStudio />
            </ProtectedRoute>
          }
        />
        <Route
          path="/tools/swagger-ui"
          element={
            <ProtectedRoute requireAdmin={true}>
              <SwaggerUI />
            </ProtectedRoute>
          }
        />
        <Route
          path="/perfect-world/game-offsets"
          element={
            <ProtectedRoute requireAdmin={true}>
              <GameOffsets />
            </ProtectedRoute>
          }
        />
        <Route
          path="/perfect-world/game-roles"
          element={
            <ProtectedRoute requireAdmin={true}>
              <GameRoles />
            </ProtectedRoute>
          }
        />
        <Route
          path="/perfect-world/state-machine"
          element={
            <ProtectedRoute requireAdmin={true}>
              <StateMachine />
            </ProtectedRoute>
          }
        />
        
        {/* Protected routes */}
        <Route
          path="/dashboard"
          element={
            <ProtectedRoute>
              <Dashboard />
            </ProtectedRoute>
          }
        />
        <Route
          path="/profile"
          element={
            <ProtectedRoute>
              <Profile />
            </ProtectedRoute>
          }
        />
        {/* Users route removed - user management is handled by Kanidm OIDC */}
        </Routes>
      </main>
      <Footer />
     <PWAInstallPrompt />
   </div>
  );
};

function App() {
  return (
    <AuthProvider>
      <Router>
        <AppContent />
      </Router>
    </AuthProvider>
  );
}

export default App;
