import React, { useState, useCallback } from 'react';

const Encoding = () => {
  const [inputText, setInputText] = useState('');
  const [outputText, setOutputText] = useState('');
  const [selectedType, setSelectedType] = useState('base64');
  const [operation, setOperation] = useState('encode');
  const [unicodeMode, setUnicodeMode] = useState(false);

  const encodingTypes = [
    { value: 'base64', label: 'Base64', icon: '🔤' },
    { value: 'html', label: 'HTML', icon: '🌐' },
    { value: 'url', label: 'URL', icon: '🔗' },
    { value: 'hex', label: 'Hex', icon: '🔢' }
  ];

  const handleEncode = useCallback(() => {
    if (!inputText.trim()) {
      setOutputText('');
      return;
    }

    try {
      let result = '';
      switch (selectedType) {
        case 'base64':
          if (operation === 'encode') {
            if (unicodeMode) {
              // Unicode (UTF-16) encoding
              const utf16Bytes = [];
              for (let i = 0; i < inputText.length; i++) {
                const charCode = inputText.charCodeAt(i);
                utf16Bytes.push(charCode & 0xFF); // Low byte
                utf16Bytes.push((charCode >> 8) & 0xFF); // High byte
              }
              const binaryString = String.fromCharCode(...utf16Bytes);
              result = btoa(binaryString);
            } else {
              // Non-Unicode (UTF-8) encoding
              result = btoa(unescape(encodeURIComponent(inputText)));
            }
          } else {
            if (unicodeMode) {
              // Unicode (UTF-16) decoding
              try {
                const binaryString = atob(inputText);
                const bytes = [];
                for (let i = 0; i < binaryString.length; i++) {
                  bytes.push(binaryString.charCodeAt(i));
                }
                let decodedResult = '';
                for (let i = 0; i < bytes.length; i += 2) {
                  const charCode = bytes[i] | (bytes[i + 1] << 8);
                  decodedResult += String.fromCharCode(charCode);
                }
                result = decodedResult;
              } catch (error) {
                throw new Error('Invalid Unicode Base64 string');
              }
            } else {
              // Non-Unicode (UTF-8) decoding
              result = decodeURIComponent(escape(atob(inputText)));
            }
          }
          break;
        case 'html':
          if (operation === 'encode') {
            result = inputText
              .replace(/&/g, '&amp;')
              .replace(/</g, '&lt;')
              .replace(/>/g, '&gt;')
              .replace(/"/g, '&quot;')
              .replace(/'/g, '&#39;');
          } else {
            result = inputText
              .replace(/&amp;/g, '&')
              .replace(/&lt;/g, '<')
              .replace(/&gt;/g, '>')
              .replace(/&quot;/g, '"')
              .replace(/&#39;/g, "'");
          }
          break;
        case 'url':
          result = operation === 'encode' 
            ? encodeURIComponent(inputText)
            : decodeURIComponent(inputText);
          break;
        case 'hex':
          if (operation === 'encode') {
            if (unicodeMode) {
              // Unicode (UTF-16) hex encoding
              result = Array.from(inputText)
                .map(char => {
                  const charCode = char.charCodeAt(0);
                  return charCode.toString(16).padStart(4, '0');
                })
                .join('');
            } else {
              // Non-Unicode (UTF-8) hex encoding
              result = Array.from(inputText)
                .map(char => char.charCodeAt(0).toString(16).padStart(2, '0'))
                .join('');
            }
          } else {
            if (unicodeMode) {
              // Unicode (UTF-16) hex decoding
              result = inputText
                .match(/.{1,4}/g)
                ?.map(hex => String.fromCharCode(parseInt(hex, 16)))
                .join('') || '';
            } else {
              // Non-Unicode (UTF-8) hex decoding
              result = inputText
                .match(/.{1,2}/g)
                ?.map(hex => String.fromCharCode(parseInt(hex, 16)))
                .join('') || '';
            }
          }
          break;
        default:
          result = '';
      }
      setOutputText(result);
    } catch (error) {
      setOutputText(`Error: ${error.message}`);
    }
  }, [inputText, selectedType, operation, unicodeMode]);

  const handleCopy = () => {
    navigator.clipboard.writeText(outputText);
  };

  const handleClear = () => {
    setInputText('');
    setOutputText('');
  };

  const handleSwap = () => {
    setInputText(outputText);
    setOutputText(inputText);
    setOperation(operation === 'encode' ? 'decode' : 'encode');
  };

  React.useEffect(() => {
    handleEncode();
  }, [handleEncode]);

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Encoding Tools</h1>
          <p className="text-gray-600">Encode and decode text using various encoding methods</p>
        </div>

        <div className="bg-white shadow-lg rounded-lg p-6">
          {/* Compact Options Bar */}
          <div className="mb-6 bg-gray-50 rounded-lg p-4">
            <div className="flex flex-wrap items-center gap-4">
              {/* Encoding Type Selection (Combobox) */}
              <div className="flex items-center space-x-2">
                <label htmlFor="encoding-type" className="text-sm font-medium text-gray-700">
                  Type:
                </label>
                <select
                  id="encoding-type"
                  value={selectedType}
                  onChange={(e) => setSelectedType(e.target.value)}
                  className="px-3 py-2 rounded-md text-sm border border-gray-300 bg-white shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                >
                  {encodingTypes.map((type) => (
                    <option key={type.value} value={type.value}>
                      {type.label}
                    </option>
                  ))}
                </select>
              </div>

              {/* Operation Selection */}
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium text-gray-700">Mode:</span>
                <div className="flex space-x-1">
                  <button
                    onClick={() => setOperation('encode')}
                    className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                      operation === 'encode'
                        ? 'bg-green-600 text-white'
                        : 'bg-white text-gray-700 hover:bg-gray-100 border border-gray-300'
                    }`}
                  >
                    Encode
                  </button>
                  <button
                    onClick={() => setOperation('decode')}
                    className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                      operation === 'decode'
                        ? 'bg-red-600 text-white'
                        : 'bg-white text-gray-700 hover:bg-gray-100 border border-gray-300'
                    }`}
                  >
                    Decode
                  </button>
                </div>
              </div>

              {/* Unicode Mode Selection (for Base64 and Hex) */}
              {(selectedType === 'base64' || selectedType === 'hex') && (
                <div className="flex items-center space-x-2">
                  <span className="text-sm font-medium text-gray-700">Unicode:</span>
                  <div className="flex space-x-1">
                    <button
                      onClick={() => setUnicodeMode(false)}
                      className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                        !unicodeMode
                          ? 'bg-blue-600 text-white'
                          : 'bg-white text-gray-700 hover:bg-gray-100 border border-gray-300'
                      }`}
                    >
                      UTF-8
                    </button>
                    <button
                      onClick={() => setUnicodeMode(true)}
                      className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                        unicodeMode
                          ? 'bg-purple-600 text-white'
                          : 'bg-white text-gray-700 hover:bg-gray-100 border border-gray-300'
                      }`}
                    >
                      UTF-16
                    </button>
                  </div>
                </div>
              )}

              {/* Action Buttons */}
              <div className="flex items-center space-x-2 ml-auto">
                <button
                  onClick={handleSwap}
                  className="px-3 py-1 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors text-sm font-medium flex items-center space-x-1"
                  title="Swap input and output"
                >
                  <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7h12m0 0l-4-4m4 4l-4 4m0 6H4m0 0l4 4m-4-4l4-4" />
                  </svg>
                  <span>Swap</span>
                </button>
                <button
                  onClick={handleClear}
                  className="px-3 py-1 bg-gray-600 text-white rounded-md hover:bg-gray-700 transition-colors text-sm font-medium flex items-center space-x-1"
                  title="Clear all"
                >
                  <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                  </svg>
                  <span>Clear</span>
                </button>
              </div>
            </div>
          </div>

          {/* Input/Output Grid */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
            {/* Input Section */}
            <div>
              <div className="flex justify-between items-center mb-2">
                <label className="block text-sm font-medium text-gray-700">
                  Input Text
                </label>
                <span className="text-xs text-gray-500">
                  {inputText.length} characters
                </span>
              </div>
              <textarea
                value={inputText}
                onChange={(e) => setInputText(e.target.value)}
                placeholder={`Enter text to ${operation}...`}
                className="w-full h-64 p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent resize-none font-mono text-sm"
              />
            </div>

            {/* Output Section */}
            <div>
              <div className="flex justify-between items-center mb-2">
                <label className="block text-sm font-medium text-gray-700">
                  Output Text
                </label>
                <div className="flex items-center space-x-2">
                  <span className="text-xs text-gray-500">
                    {outputText.length} characters
                  </span>
                  <button
                    onClick={handleCopy}
                    disabled={!outputText}
                    className="px-3 py-1 text-sm bg-blue-100 text-blue-700 rounded hover:bg-blue-200 transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex items-center space-x-1"
                    title="Copy to clipboard"
                  >
                    <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                    </svg>
                    <span>Copy</span>
                  </button>
                </div>
              </div>
              <textarea
                value={outputText}
                readOnly
                placeholder={`${operation === 'encode' ? 'Encoded' : 'Decoded'} text will appear here...`}
                className="w-full h-64 p-4 border border-gray-300 rounded-lg bg-gray-50 resize-none font-mono text-sm"
              />
            </div>
          </div>

          {/* Compact Information Section */}
          <div className="bg-gray-50 border border-gray-200 rounded-lg p-3">
            <div className="flex items-start space-x-2">
              <svg className="w-4 h-4 text-blue-600 mt-0.5 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
              </svg>
              <div className="text-sm text-gray-700">
                {selectedType === 'base64' && (
                  <div>
                    <span className="font-medium">Base64:</span> Binary-to-text encoding. 
                    {unicodeMode ? ' UTF-16 mode (2 bytes/char)' : ' UTF-8 mode (1-4 bytes/char)'}
                  </div>
                )}
                {selectedType === 'html' && (
                  <div>
                    <span className="font-medium">HTML:</span> Converts special characters to HTML entities (&amp;, &lt;, &gt;, &quot;, &#39;)
                  </div>
                )}
                {selectedType === 'url' && (
                  <div>
                    <span className="font-medium">URL:</span> Percent-encodes special characters for web transmission (space → %20)
                  </div>
                )}
                {selectedType === 'hex' && (
                  <div>
                    <span className="font-medium">Hex:</span> Represents each character as hexadecimal. 
                    {unicodeMode ? ' UTF-16 mode (4 hex digits/char)' : ' UTF-8 mode (2 hex digits/char)'}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Encoding;
