import React from 'react';
import { Navigate, useLocation } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import LoadingSpinner from './LoadingSpinner';

// Define whitelist pages that don't require authentication
const WHITELIST_PAGES = [
  '/',
  '/login',
  '/register',
  '/about',
  '/contact'
];

const ProtectedRoute = ({ children, requireAuth = true, requireAdmin = false }) => {
  const { isAuthenticated, loading, user } = useAuth();
  const location = useLocation();
  const currentPath = location.pathname;

  // Check if user is admin based on groups
  const isAdmin = user?.groups?.some(group => 
    group.toLowerCase().includes('admin') || 
    group.toLowerCase().includes('administrator')
  ) || false;

  // Check if current page is in whitelist
  const isWhitelisted = WHITELIST_PAGES.includes(currentPath);

  // Show loading spinner while checking authentication
  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <LoadingSpinner size="xl" text="Checking authentication..." />
      </div>
    );
  }

  // If page requires authentication and user is not authenticated
  if (requireAuth && !isAuthenticated && !isWhitelisted) {
    // Redirect to login page with return URL
    return <Navigate to="/login" state={{ from: location }} replace />;
  }

  // If page requires admin access and user is not admin
  if (requireAdmin && isAuthenticated && !isAdmin) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="bg-white shadow rounded-lg p-8 max-w-md w-full mx-4">
          <div className="text-center">
            <svg className="mx-auto h-12 w-12 text-red-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.732-.833-2.5 0L4.232 15.5c-.77.833.192 2.5 1.732 2.5z" />
            </svg>
            <h3 className="mt-2 text-lg font-medium text-gray-900">Access Denied</h3>
            <p className="mt-1 text-sm text-gray-500">
              You need administrator privileges to access this page.
            </p>
            <div className="mt-4">
              <button
                onClick={() => window.history.back()}
                className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-blue-600 bg-blue-50 hover:bg-blue-100 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
              >
                Go Back
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // If user is authenticated and trying to access login/register pages
  if (isAuthenticated && (currentPath === '/login' || currentPath === '/register')) {
    // Redirect to dashboard
    return <Navigate to="/dashboard" replace />;
  }

  // Allow access to the requested page
  return children;
};

export default ProtectedRoute;
