import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { handleCallback } from '../services/oidcService';
import LoadingSpinner from '../components/LoadingSpinner';
import { useAuth } from '../contexts/AuthContext';

const OidcCallback = () => {
  const navigate = useNavigate();
  const [error, setError] = useState(null);
  const { clearError, completeOidcLogin } = useAuth();

  useEffect(() => {
    let isMounted = true;
    (async () => {
      try {
        const { returnTo, user } = await handleCallback();
        try { clearError(); } catch (_) {}
        // Immediately set user in context to avoid a flicker back to /login
        try { completeOidcLogin(user); } catch (_) {}
        if (isMounted) navigate(returnTo, { replace: true });
      } catch (e) {
        if (isMounted) setError(e.message || 'Login failed');
      }
    })();
    return () => { isMounted = false; };
  }, [navigate]);

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="max-w-md w-full bg-white shadow rounded p-6 text-center">
          <h2 className="text-lg font-semibold text-red-600 mb-2">Authentication Error</h2>
          <p className="text-gray-700 mb-4">{error}</p>
          <button onClick={() => navigate('/login', { replace: true })} className="px-4 py-2 bg-blue-600 text-white rounded">Back to Login</button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex items-center justify-center">
      <LoadingSpinner size="xl" text="Finalizing sign-in..." />
    </div>
  );
};

export default OidcCallback;



