import React, { useState, useEffect, useRef } from 'react';

const Mines = () => {
  const [game, setGame] = useState(null);
  const [gameState, setGameState] = useState('waiting');
  const [board, setBoard] = useState([]);
  const [mineCount, setMineCount] = useState(99);
  const [flagCount, setFlagCount] = useState(0);
  const [revealedCount, setRevealedCount] = useState(0);
  const [timer, setTimer] = useState(0);
  const [currentDifficulty, setCurrentDifficulty] = useState('expert');
  const [gameStatus, setGameStatus] = useState('');
  const [startTime, setStartTime] = useState(null);
  const timerRef = useRef(null);
  const touchStartTimeRef = useRef(0);
  const touchTimerRef = useRef(null);
  const isLongPressRef = useRef(false);
  const touchMovedRef = useRef(false);
  const longPressActionExecutedRef = useRef(false);
  const lastLongPressTimeRef = useRef(0);
  const touchStartXRef = useRef(0);
  const touchStartYRef = useRef(0);

  const difficulties = {
    beginner: { rows: 9, cols: 9, mines: 10 },
    intermediate: { rows: 16, cols: 16, mines: 40 },
    expert: { rows: 16, cols: 30, mines: 99 }
  };

  const longPressDelay = 500; // 500ms for long press
  const longPressCooldown = 200; // 200ms cooldown after long press
  const moveThreshold = 15; // pixels to consider as movement (increased for better scroll detection)

  useEffect(() => {
    // Load saved difficulty
    const savedDifficulty = localStorage.getItem('minesweeper-difficulty');
    if (savedDifficulty && difficulties[savedDifficulty]) {
      setCurrentDifficulty(savedDifficulty);
    }
  }, []);

  useEffect(() => {
    resetGame();
  }, [currentDifficulty]);

  useEffect(() => {
    if (gameState === 'playing' && startTime) {
      timerRef.current = setInterval(() => {
        const elapsed = Math.floor((Date.now() - startTime) / 1000);
        setTimer(elapsed);
      }, 1000);
    } else if (timerRef.current) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }

    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
    };
  }, [gameState, startTime]);

  // Check for win condition whenever board or revealed count changes
  useEffect(() => {
    if (gameState !== 'playing') return;
    
    const config = difficulties[currentDifficulty];
    const totalCells = config.rows * config.cols;
    
    // Check win condition: all non-mine cells revealed
    if (revealedCount === totalCells - mineCount) {
      setGameState('won');
      setGameStatus('🎉 Congratulations! You won!');
      // Auto-flag all remaining mines when game is won
      flagAllMines();
    }
  }, [board, revealedCount, gameState, mineCount, currentDifficulty]);

  const createBoard = (rows, cols, mineCount) => {
    const newBoard = [];
    
    // Initialize empty board
    for (let r = 0; r < rows; r++) {
      newBoard[r] = [];
      for (let c = 0; c < cols; c++) {
        newBoard[r][c] = {
          isMine: false,
          isRevealed: false,
          isFlagged: false,
          adjacentMines: 0
        };
      }
    }
    
    // Place mines randomly
    let minesPlaced = 0;
    while (minesPlaced < mineCount) {
      const row = Math.floor(Math.random() * rows);
      const col = Math.floor(Math.random() * cols);
      
      if (!newBoard[row][col].isMine) {
        newBoard[row][col].isMine = true;
        minesPlaced++;
      }
    }
    
    // Calculate adjacent mine counts
    for (let r = 0; r < rows; r++) {
      for (let c = 0; c < cols; c++) {
        if (!newBoard[r][c].isMine) {
          newBoard[r][c].adjacentMines = countAdjacentMines(newBoard, r, c);
        }
      }
    }
    
    return newBoard;
  };

  const countAdjacentMines = (board, row, col) => {
    let count = 0;
    for (let r = row - 1; r <= row + 1; r++) {
      for (let c = col - 1; c <= col + 1; c++) {
        if (r >= 0 && r < board.length && 
            c >= 0 && c < board[0].length && 
            board[r][c].isMine) {
          count++;
        }
      }
    }
    return count;
  };

  const ensureFirstClickSafe = (clickRow, clickCol) => {
    const config = difficulties[currentDifficulty];
    const minesToRelocate = [];
    const newBoard = [...board];
    
    // Check the clicked cell and all its neighbors for mines
    for (let r = clickRow - 1; r <= clickRow + 1; r++) {
      for (let c = clickCol - 1; c <= clickCol + 1; c++) {
        if (r >= 0 && r < config.rows && 
            c >= 0 && c < config.cols && 
            newBoard[r][c].isMine) {
          // Mark this mine for relocation
          minesToRelocate.push({row: r, col: c});
          newBoard[r][c].isMine = false;
        }
      }
    }
    
    // Relocate mines to safe positions
    for (let mineData of minesToRelocate) {
      relocateMine(newBoard, clickRow, clickCol);
    }
    
    // Recalculate adjacent mine counts for the entire board
    recalculateAdjacentMines(newBoard);
    setBoard(newBoard);
  };

  const relocateMine = (board, avoidRow, avoidCol) => {
    const config = difficulties[currentDifficulty];
    let placed = false;
    let attempts = 0;
    const maxAttempts = config.rows * config.cols;
    
    while (!placed && attempts < maxAttempts) {
      const newRow = Math.floor(Math.random() * config.rows);
      const newCol = Math.floor(Math.random() * config.cols);
      
      // Check if this position is safe (not in the avoid area and not already a mine)
      const inAvoidArea = Math.abs(newRow - avoidRow) <= 1 && Math.abs(newCol - avoidCol) <= 1;
      
      if (!inAvoidArea && !board[newRow][newCol].isMine) {
        board[newRow][newCol].isMine = true;
        placed = true;
      }
      
      attempts++;
    }
  };

  const recalculateAdjacentMines = (board) => {
    const config = difficulties[currentDifficulty];
    
    for (let r = 0; r < config.rows; r++) {
      for (let c = 0; c < config.cols; c++) {
        if (!board[r][c].isMine) {
          board[r][c].adjacentMines = countAdjacentMines(board, r, c);
        }
      }
    }
  };

  const resetGame = () => {
    setGameState('waiting');
    setStartTime(null);
    setFlagCount(0);
    setRevealedCount(0);
    setTimer(0);
    setGameStatus('');
    
    // Clean up touch timer and reset touch state
    if (touchTimerRef.current) {
      clearTimeout(touchTimerRef.current);
      touchTimerRef.current = null;
    }
    isLongPressRef.current = false;
    touchMovedRef.current = false;
    longPressActionExecutedRef.current = false;
    lastLongPressTimeRef.current = 0;
    
    const config = difficulties[currentDifficulty];
    setMineCount(config.mines);
    
    const newBoard = createBoard(config.rows, config.cols, config.mines);
    setBoard(newBoard);
  };

  const handleDifficultyChange = (difficulty) => {
    setCurrentDifficulty(difficulty);
    localStorage.setItem('minesweeper-difficulty', difficulty);
  };

  const handleCellClick = (row, col) => {
    if (gameState === 'won' || gameState === 'lost') return;
    
    const cell = board[row][col];
    if (cell.isRevealed || cell.isFlagged) return;
    
    if (gameState === 'waiting') {
      // Ensure first click is safe
      ensureFirstClickSafe(row, col);
      setGameState('playing');
      setStartTime(Date.now());
    }
    
    revealCell(row, col);
  };

  const handleCellRightClick = (e, row, col) => {
    e.preventDefault();
    
    if (gameState === 'won' || gameState === 'lost') return;
    
    const cell = board[row][col];
    if (cell.isRevealed) return;
    
    if (gameState === 'waiting') {
      // Even if flagging first, ensure the area is safe for future clicks
      ensureFirstClickSafe(row, col);
      setGameState('playing');
      setStartTime(Date.now());
    }
    
    const newBoard = [...board];
    newBoard[row][col].isFlagged = !newBoard[row][col].isFlagged;
    setBoard(newBoard);
    setFlagCount(prev => prev + (newBoard[row][col].isFlagged ? 1 : -1));
  };

  const handleTouchStart = (e, row, col) => {
    if (gameState === 'won' || gameState === 'lost') return;
    
    touchStartTimeRef.current = Date.now();
    isLongPressRef.current = false;
    touchMovedRef.current = false;
    longPressActionExecutedRef.current = false;
    touchStartXRef.current = e.touches[0].clientX;
    touchStartYRef.current = e.touches[0].clientY;
    
    // Start timer for long press detection
    touchTimerRef.current = setTimeout(() => {
      isLongPressRef.current = true;
      longPressActionExecutedRef.current = true;
      lastLongPressTimeRef.current = Date.now();
      // Trigger flag action on long press
      if (!touchMovedRef.current) {
        e.preventDefault(); // Only prevent default when we're flagging
        handleCellRightClick(e, row, col);
        // Provide haptic feedback if available
        if (navigator.vibrate) {
          navigator.vibrate(50);
        }
      }
    }, longPressDelay);
  };

  const handleTouchEnd = (e, row, col) => {
    // Always prevent default if a long press action was executed
    if (longPressActionExecutedRef.current) {
      e.preventDefault();
    }
    
    if (touchTimerRef.current) {
      clearTimeout(touchTimerRef.current);
      touchTimerRef.current = null;
    }
    
    // Check if we're in cooldown period after a long press
    const timeSinceLastLongPress = Date.now() - lastLongPressTimeRef.current;
    const inCooldownPeriod = timeSinceLastLongPress < longPressCooldown;
    
    // Only allow reveal if:
    // 1. It was NOT a long press
    // 2. User did NOT move their finger significantly  
    // 3. NO long press action was executed during this touch sequence
    // 4. We're NOT in the cooldown period after a recent long press
    if (!isLongPressRef.current && !touchMovedRef.current && !longPressActionExecutedRef.current && !inCooldownPeriod) {
      e.preventDefault();
      handleCellClick(row, col);
    }
    
    // Reset touch state (but keep lastLongPressTime for cooldown)
    touchStartTimeRef.current = 0;
    isLongPressRef.current = false;
    touchMovedRef.current = false;
    longPressActionExecutedRef.current = false;
    touchStartXRef.current = 0;
    touchStartYRef.current = 0;
  };

  const handleTouchMove = (e) => {
    // Calculate movement distance
    const currentX = e.touches[0].clientX;
    const currentY = e.touches[0].clientY;
    const deltaX = Math.abs(currentX - touchStartXRef.current);
    const deltaY = Math.abs(currentY - touchStartYRef.current);
    
    // If movement exceeds threshold, consider it as intentional movement
    if (deltaX > moveThreshold || deltaY > moveThreshold) {
      touchMovedRef.current = true;
      longPressActionExecutedRef.current = false; // Reset since movement cancels action
      // Cancel long press if finger moves significantly
      if (touchTimerRef.current) {
        clearTimeout(touchTimerRef.current);
        touchTimerRef.current = null;
      }
    }
  };

  const revealCell = (row, col) => {
    const cell = board[row][col];
    if (cell.isRevealed || cell.isFlagged) return;
    
    const newBoard = [...board];
    newBoard[row][col].isRevealed = true;
    setBoard(newBoard);
    setRevealedCount(prev => prev + 1);
    
    if (cell.isMine) {
      setGameState('lost');
      revealAllMines();
      setGameStatus('💥 Game Over! Better luck next time!');
      return;
    }
    
    // Auto-reveal adjacent cells if no adjacent mines
    if (cell.adjacentMines === 0) {
      for (let r = row - 1; r <= row + 1; r++) {
        for (let c = col - 1; c <= col + 1; c++) {
          if (r >= 0 && r < board.length && 
              c >= 0 && c < board[0].length) {
            revealCell(r, c);
          }
        }
      }
    }
  };

  const revealAllMines = () => {
    const newBoard = [...board];
    for (let r = 0; r < board.length; r++) {
      for (let c = 0; c < board[0].length; c++) {
        if (newBoard[r][c].isMine) {
          newBoard[r][c].isRevealed = true;
        }
      }
    }
    setBoard(newBoard);
  };

  const flagAllMines = () => {
    const newBoard = [...board];
    let newFlagCount = flagCount;
    for (let r = 0; r < board.length; r++) {
      for (let c = 0; c < board[0].length; c++) {
        if (newBoard[r][c].isMine && !newBoard[r][c].isFlagged) {
          newBoard[r][c].isFlagged = true;
          newFlagCount++;
        }
      }
    }
    setBoard(newBoard);
    setFlagCount(newFlagCount);
  };


  const getCellContent = (cell) => {
    if (cell.isFlagged) {
      return '🚩';
    } else if (cell.isRevealed) {
      if (cell.isMine) {
        return '💣';
      } else if (cell.adjacentMines > 0) {
        return cell.adjacentMines;
      }
    }
    return '';
  };

  const getCellClassName = (cell) => {
    let className = 'cell';
    if (cell.isFlagged) {
      className += ' flagged';
    } else if (cell.isRevealed) {
      className += ' revealed';
      if (cell.isMine) {
        className += ' mine';
      } else if (cell.adjacentMines > 0) {
        className += ` cell-${cell.adjacentMines}`;
      }
    }
    return className;
  };

  const config = difficulties[currentDifficulty];

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-500 to-purple-600 py-1 px-1 sm:py-2 sm:px-2 md:py-8 md:px-4">
      <div className="max-w-6xl mx-auto">
        <div className="bg-white rounded-lg sm:rounded-2xl shadow-2xl p-2 sm:p-4 md:p-8">
          {/* Header */}
          <div className="text-center mb-2 sm:mb-4 md:mb-8">
            <h1 className="text-2xl sm:text-3xl md:text-5xl font-bold text-gray-800 mb-2 sm:mb-3 md:mb-6">💣 Minesweeper</h1>
            
            {/* Controls */}
            <div className="flex justify-center items-center gap-2 sm:gap-3 md:gap-6 mb-2 sm:mb-4 md:mb-6 flex-wrap">
              <div className="flex gap-1 sm:gap-2 md:gap-3 flex-wrap">
                {Object.keys(difficulties).map((difficulty) => (
                  <button
                    key={difficulty}
                    onClick={() => handleDifficultyChange(difficulty)}
                    className={`px-2 sm:px-4 md:px-6 py-1 sm:py-2 md:py-3 rounded-full font-bold text-sm sm:text-base md:text-lg transition-all duration-300 ${
                      currentDifficulty === difficulty
                        ? 'bg-blue-600 text-white transform -translate-y-1'
                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                    }`}
                  >
                    {difficulty.charAt(0).toUpperCase() + difficulty.slice(1)}
                  </button>
                ))}
              </div>
              
              <button
                onClick={resetGame}
                className="px-3 sm:px-6 md:px-8 py-1 sm:py-2 md:py-3 bg-green-600 hover:bg-green-700 text-white font-bold rounded-full transition-all duration-300 transform hover:-translate-y-1 text-sm sm:text-base md:text-lg"
              >
                New Game
              </button>
            </div>
            
            {/* Game Stats */}
            <div className="flex justify-center gap-2 sm:gap-4 md:gap-8 mb-2 sm:mb-4 md:mb-6 flex-wrap">
              <div className="bg-gray-50 px-2 sm:px-4 md:px-6 py-2 sm:py-3 md:py-4 rounded-lg min-w-16 sm:min-w-20 md:min-w-24">
                <div className="text-sm sm:text-sm md:text-base text-gray-600 uppercase font-bold mb-1">Mines</div>
                <div className="text-xl sm:text-2xl md:text-3xl font-bold text-gray-800">
                  {Math.max(0, mineCount - flagCount)}
                </div>
              </div>
              <div className="bg-gray-50 px-2 sm:px-4 md:px-6 py-2 sm:py-3 md:py-4 rounded-lg min-w-16 sm:min-w-20 md:min-w-24">
                <div className="text-sm sm:text-sm md:text-base text-gray-600 uppercase font-bold mb-1">Time</div>
                <div className="text-xl sm:text-2xl md:text-3xl font-bold text-gray-800">
                  {timer.toString().padStart(3, '0')}
                </div>
              </div>
            </div>
          </div>
          
          {/* Game Board Container */}
          <div className="flex flex-col items-center mb-2 sm:mb-4 md:mb-6">
            {config.cols > 16 && (
              <div className="text-sm sm:text-sm md:text-base text-gray-500 mb-1 sm:mb-2 md:mb-3 text-center px-2">
                📱 Swipe left/right to scroll the game board
              </div>
            )}
            <div className="flex justify-center overflow-x-auto game-board-container w-full -mx-1 sm:-mx-2 md:mx-0">
              <div 
                className="inline-grid gap-px bg-gray-400 border-2 border-gray-400 rounded-lg p-1"
                style={{ 
                  gridTemplateColumns: `repeat(${config.cols}, 1fr)`,
                  minWidth: 'fit-content'
                }}
              >
              {board.map((row, rowIndex) =>
                row.map((cell, colIndex) => (
                  <div
                    key={`${rowIndex}-${colIndex}`}
                    className={getCellClassName(cell)}
                    onClick={() => handleCellClick(rowIndex, colIndex)}
                    onContextMenu={(e) => handleCellRightClick(e, rowIndex, colIndex)}
                    onTouchStart={(e) => handleTouchStart(e, rowIndex, colIndex)}
                    onTouchEnd={(e) => handleTouchEnd(e, rowIndex, colIndex)}
                    onTouchMove={handleTouchMove}
                    style={{
                      width: config.cols > 20 ? '28px' : config.cols > 10 ? '32px' : '36px',
                      height: config.cols > 20 ? '28px' : config.cols > 10 ? '32px' : '36px',
                      fontSize: config.cols > 20 ? '12px' : config.cols > 10 ? '14px' : '16px',
                      fontWeight: 'bold',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      cursor: 'pointer',
                      transition: 'all 0.1s ease',
                      userSelect: 'none',
                      WebkitUserSelect: 'none',
                      WebkitTouchCallout: 'none',
                      WebkitTapHighlightColor: 'transparent',
                      outline: 'none',
                      minWidth: '28px',
                      minHeight: '28px'
                    }}
                  >
                    {getCellContent(cell)}
                  </div>
                ))
              )}
              </div>
            </div>
          </div>
          
          {/* Game Status */}
          <div className={`text-center mb-2 sm:mb-4 md:mb-6 text-base sm:text-xl md:text-2xl font-bold min-h-4 sm:min-h-6 md:min-h-8 ${
            gameState === 'won' ? 'text-green-600' : 
            gameState === 'lost' ? 'text-red-600' : 
            'text-gray-600'
          }`}>
            {gameStatus}
          </div>
          
          {/* Instructions */}
          <div className="text-sm sm:text-base md:text-lg text-gray-600 max-w-3xl mx-auto text-center px-2">
            <strong>How to play:</strong><br />
            Left-click to reveal a cell • Right-click to flag/unflag a mine<br />
            <strong>Mobile:</strong> Tap to reveal • Hold for 0.5s to flag<br />
            Numbers show how many mines are adjacent • Find all mines to win!<br />
            <strong>First click is always safe!</strong> No mines will explode on your first move.
          </div>
        </div>
      </div>

      <style jsx>{`
        .cell {
          background: #c0c0c0;
          border: none;
          font-weight: bold;
          font-size: 14px;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: all 0.1s ease;
          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
          -webkit-touch-callout: none;
          -webkit-tap-highlight-color: transparent;
          outline: none;
        }

        .cell:hover {
          background: #d0d0d0;
        }

        .cell.revealed {
          background: #e0e0e0 !important;
          cursor: default;
        }

        .cell.flagged {
          background: #ffeb3b !important;
          color: #d32f2f !important;
        }

        .cell.mine {
          background: #f44336;
          color: white;
        }

        .cell-1 { color: #1976d2; }
        .cell-2 { color: #388e3c; }
        .cell-3 { color: #d32f2f; }
        .cell-4 { color: #7b1fa2; }
        .cell-5 { color: #689f38; }
        .cell-6 { color: #0097a7; }
        .cell-7 { color: #5d4037; }
        .cell-8 { color: #424242; }

        /* Disable hover on touch devices to prevent stuck hover states */
        @media (hover: none) and (pointer: coarse) {
          .cell:not(.flagged):not(.revealed):hover {
            background: #c0c0c0 !important;
          }
          
          .cell.revealed:hover {
            background: #e0e0e0 !important;
          }
          
          .cell.flagged:hover {
            background: #ffeb3b !important;
            color: #d32f2f !important;
          }
        }

        /* Desktop optimizations */
        @media (min-width: 768px) {
          .cell {
            width: 36px !important;
            height: 36px !important;
            font-size: 16px !important;
            min-width: 36px !important;
            min-height: 36px !important;
          }
        }

        /* Small screens (tablets and small phones) */
        @media (max-width: 767px) {
          .cell {
            width: 34px !important;
            height: 34px !important;
            font-size: 14px !important;
            min-width: 34px !important;
            min-height: 34px !important;
          }
        }

        /* Very small screens */
        @media (max-width: 480px) {
          .cell {
            width: 32px !important;
            height: 32px !important;
            font-size: 13px !important;
            min-width: 32px !important;
            min-height: 32px !important;
          }
        }

        /* Extra small screens */
        @media (max-width: 360px) {
          .cell {
            width: 29px !important;
            height: 29px !important;
            font-size: 12px !important;
            min-width: 29px !important;
            min-height: 29px !important;
          }
        }

        /* Mobile game board scrolling */
        @media (max-width: 1024px) {
          .game-board-container {
            overflow-x: auto;
            overflow-y: hidden;
            -webkit-overflow-scrolling: touch;
            scrollbar-width: thin;
            scrollbar-color: #cbd5e0 #f7fafc;
          }
          
          .game-board-container::-webkit-scrollbar {
            height: 8px;
          }
          
          .game-board-container::-webkit-scrollbar-track {
            background: #f7fafc;
            border-radius: 4px;
          }
          
          .game-board-container::-webkit-scrollbar-thumb {
            background: #cbd5e0;
            border-radius: 4px;
          }
          
          .game-board-container::-webkit-scrollbar-thumb:hover {
            background: #a0aec0;
          }
        }

        /* Touch device optimizations */
        @media (hover: none) and (pointer: coarse) {
          .game-board-container {
            touch-action: pan-x pan-y;
            -webkit-overflow-scrolling: touch;
          }
          
          .cell {
            touch-action: manipulation;
            -webkit-tap-highlight-color: transparent;
          }
        }

        /* Mobile-specific optimizations */
        @media (max-width: 768px) {
          .game-board-container {
            padding: 0 4px;
            margin: 0 -4px;
          }
          
          .game-board-container::-webkit-scrollbar {
            height: 4px;
          }
        }

        /* Very small screens - maximize board space */
        @media (max-width: 480px) {
          .game-board-container {
            padding: 0 2px;
            margin: 0 -2px;
          }
          
          .game-board-container::-webkit-scrollbar {
            height: 3px;
          }
        }

        /* Extra small screens - minimal padding */
        @media (max-width: 360px) {
          .game-board-container {
            padding: 0 1px;
            margin: 0 -1px;
          }
        }
      `}</style>
    </div>
  );
};

export default Mines;
