import React, { useState, useRef, useEffect, useMemo } from 'react';

const MediaPlayer = () => {
  const [currentTrack, setCurrentTrack] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [volume, setVolume] = useState(1);
  const [showPlaylist, setShowPlaylist] = useState(false);
  const [mediaType, setMediaType] = useState('audio'); // 'audio' or 'video'
  const [mediaError, setMediaError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isShuffled, setIsShuffled] = useState(false);
  const [loopMode, setLoopMode] = useState('none'); // 'none', 'one', 'all'
  const [shuffledPlaylist, setShuffledPlaylist] = useState([]);
  
  const audioRef = useRef(null);
  const videoRef = useRef(null);
  const progressRef = useRef(null);

  // Sample playlist with both audio and video - using working sample URLs
  const playlist = useMemo(() => [
    {
      id: 1,
      title: "Sample Audio Track 1",
      artist: "Artist Name",
      type: "audio",
      url: "https://www2.cs.uic.edu/~i101/SoundFiles/BabyElephantWalk60.wav", // Working WAV file
      thumbnail: "https://via.placeholder.com/300x200/3B82F6/ffffff?text=Audio+1"
    },
    {
      id: 2,
      title: "Sample Audio Track 2", 
      artist: "Artist Name",
      type: "audio",
      url: "https://www2.cs.uic.edu/~i101/SoundFiles/ImperialMarch60.wav", // Working WAV file
      thumbnail: "https://via.placeholder.com/300x200/10B981/ffffff?text=Audio+2"
    },
    {
      id: 3,
      title: "Sample Video",
      artist: "Video Creator",
      type: "video",
      url: "https://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4", // Working MP4 file
      thumbnail: "https://via.placeholder.com/300x200/EF4444/ffffff?text=Video+1"
    }
  ], []);

  // Get current playlist (original or shuffled)
  const currentPlaylist = isShuffled ? shuffledPlaylist : playlist;

  useEffect(() => {
    const mediaElement = mediaType === 'audio' ? audioRef.current : videoRef.current;
    
    if (mediaElement) {
      // Reset states when switching tracks
      setMediaError(null);
      setIsLoading(true);
      setCurrentTime(0);
      setDuration(0);
      
      // Pause any currently playing media before switching
      if (isPlaying) {
        mediaElement.pause();
        setIsPlaying(false);
      }
      
      const updateTime = () => setCurrentTime(mediaElement.currentTime);
      const updateDuration = () => {
        setDuration(mediaElement.duration);
        setIsLoading(false);
      };
             const handleEnded = () => {
         if (loopMode === 'one') {
           // Loop current track
           setCurrentTime(0);
           setIsPlaying(false);
           return;
         }
         
         if (loopMode === 'all' || currentTrack < currentPlaylist.length - 1) {
           // Continue to next track
           const nextTrack = (currentTrack + 1) % currentPlaylist.length;
           setCurrentTrack(nextTrack);
           setMediaType(currentPlaylist[nextTrack].type);
           setIsPlaying(false);
           setCurrentTime(0);
         } else if (loopMode === 'none') {
           // Stop at end of playlist
           setIsPlaying(false);
           setCurrentTime(0);
         }
       };
      const handleError = (e) => {
        console.error('Media error:', e);
        setMediaError('Failed to load media. Please try another track.');
        setIsLoading(false);
        setIsPlaying(false);
      };
      const handleLoadStart = () => {
        setIsLoading(true);
        setMediaError(null);
      };
      const handleCanPlay = () => {
        setIsLoading(false);
        setMediaError(null);
      };
      
      mediaElement.addEventListener('timeupdate', updateTime);
      mediaElement.addEventListener('loadedmetadata', updateDuration);
      mediaElement.addEventListener('ended', handleEnded);
      mediaElement.addEventListener('error', handleError);
      mediaElement.addEventListener('loadstart', handleLoadStart);
      mediaElement.addEventListener('canplay', handleCanPlay);
      
             return () => {
         mediaElement.removeEventListener('timeupdate', updateTime);
         mediaElement.removeEventListener('loadedmetadata', updateDuration);
         mediaElement.removeEventListener('ended', handleEnded);
         mediaElement.removeEventListener('error', handleError);
         mediaElement.removeEventListener('loadstart', handleLoadStart);
         mediaElement.removeEventListener('canplay', handleCanPlay);
       };
     }
   }, [currentTrack, mediaType, isPlaying, currentPlaylist, loopMode]);

  useEffect(() => {
    // Set up background audio for mobile
    if (audioRef.current) {
      audioRef.current.setAttribute('playsinline', '');
      audioRef.current.setAttribute('webkit-playsinline', '');
    }
  }, []);

  // Cleanup effect when component unmounts
  useEffect(() => {
    const audioElement = audioRef.current;
    const videoElement = videoRef.current;
    
    return () => {
      // Pause and cleanup media elements on unmount
      if (audioElement) {
        audioElement.pause();
        audioElement.src = '';
      }
      if (videoElement) {
        videoElement.pause();
        videoElement.src = '';
      }
    };
  }, []);

  const handlePlayPause = async () => {
    const mediaElement = mediaType === 'audio' ? audioRef.current : videoRef.current;
    
    if (!mediaElement) return;
    
    try {
      if (isPlaying) {
        mediaElement.pause();
        setIsPlaying(false);
      } else {
        await mediaElement.play();
        setIsPlaying(true);
      }
    } catch (error) {
      console.error('Media playback error:', error);
      // Handle autoplay restrictions or other errors
      if (error.name === 'AbortError') {
        console.log('Playback was interrupted');
      }
    }
  };

  const handleNext = () => {
    // Pause current media before switching
    const currentMediaElement = mediaType === 'audio' ? audioRef.current : videoRef.current;
    if (currentMediaElement) {
      currentMediaElement.pause();
    }
    
    const nextTrack = (currentTrack + 1) % currentPlaylist.length;
    setCurrentTrack(nextTrack);
    setMediaType(currentPlaylist[nextTrack].type);
    setIsPlaying(false);
    setCurrentTime(0);
  };

  const handlePrevious = () => {
    // Pause current media before switching
    const currentMediaElement = mediaType === 'audio' ? audioRef.current : videoRef.current;
    if (currentMediaElement) {
      currentMediaElement.pause();
    }
    
    const prevTrack = currentTrack === 0 ? currentPlaylist.length - 1 : currentTrack - 1;
    setCurrentTrack(prevTrack);
    setMediaType(currentPlaylist[prevTrack].type);
    setIsPlaying(false);
    setCurrentTime(0);
  };

  const handleSeek = (e) => {
    const mediaElement = mediaType === 'audio' ? audioRef.current : videoRef.current;
    const rect = progressRef.current.getBoundingClientRect();
    const percent = (e.clientX - rect.left) / rect.width;
    const newTime = percent * duration;
    mediaElement.currentTime = newTime;
    setCurrentTime(newTime);
  };

  const handleVolumeChange = (e) => {
    const newVolume = parseFloat(e.target.value);
    setVolume(newVolume);
    if (audioRef.current) audioRef.current.volume = newVolume;
    if (videoRef.current) videoRef.current.volume = newVolume;
  };

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  // Shuffle playlist function
  const handleShuffle = () => {
    if (isShuffled) {
      // Return to original order
      setIsShuffled(false);
      setCurrentTrack(0);
      setMediaType(playlist[0].type);
    } else {
      // Create shuffled playlist
      const shuffled = [...playlist].sort(() => Math.random() - 0.5);
      setShuffledPlaylist(shuffled);
      setIsShuffled(true);
      setCurrentTrack(0);
      setMediaType(shuffled[0].type);
    }
    setIsPlaying(false);
    setCurrentTime(0);
  };

  // Toggle loop mode
  const handleLoopToggle = () => {
    switch (loopMode) {
      case 'none':
        setLoopMode('all');
        break;
      case 'all':
        setLoopMode('one');
        break;
      case 'one':
        setLoopMode('none');
        break;
      default:
        setLoopMode('none');
    }
  };

  // Get loop mode icon
  const getLoopIcon = () => {
    switch (loopMode) {
      case 'none':
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
            <path d="M12 4V1L8 5l4 4V6c3.31 0 6 2.69 6 6 0 1.01-.25 1.97-.7 2.8l1.46 1.46C19.54 15.03 20 13.57 20 12c0-4.42-3.58-8-8-8zm0 14c-3.31 0-6-2.69-6-6 0-1.01.25-1.97.7-2.8L5.24 7.74C4.46 8.97 4 10.43 4 12c0 4.42 3.58 8 8 8v3l4-4-4-4v3z"/>
          </svg>
        );
      case 'all':
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
            <path d="M12 4V1L8 5l4 4V6c3.31 0 6 2.69 6 6 0 1.01-.25 1.97-.7 2.8l1.46 1.46C19.54 15.03 20 13.57 20 12c0-4.42-3.58-8-8-8zm0 14c-3.31 0-6-2.69-6-6 0-1.01.25-1.97.7-2.8L5.24 7.74C4.46 8.97 4 10.43 4 12c0 4.42 3.58 8 8 8v3l4-4-4-4v3z"/>
          </svg>
        );
      case 'one':
        return (
          <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
            <path d="M12 4V1L8 5l4 4V6c3.31 0 6 2.69 6 6 0 1.01-.25 1.97-.7 2.8l1.46 1.46C19.54 15.03 20 13.57 20 12c0-4.42-3.58-8-8-8zm0 14c-3.31 0-6-2.69-6-6 0-1.01.25-1.97.7-2.8L5.24 7.74C4.46 8.97 4 10.43 4 12c0 4.42 3.58 8 8 8v3l4-4-4-4v3z"/>
            <circle cx="12" cy="12" r="3"/>
          </svg>
        );
      default:
        return null;
    }
  };

  // Get loop mode tooltip
  const getLoopTooltip = () => {
    switch (loopMode) {
      case 'none':
        return 'No Loop';
      case 'all':
        return 'Loop All';
      case 'one':
        return 'Loop One';
      default:
        return 'No Loop';
    }
  };

  const currentMedia = currentPlaylist[currentTrack];

  return (
    <div className="min-h-screen bg-gray-900 text-white p-6">
      <div className="max-w-4xl mx-auto">
                 {/* Header */}
         <div className="text-center mb-8">
           <h1 className="text-4xl font-bold mb-2">Media Player</h1>
           <p className="text-gray-400">Listen to music and watch videos with background playback</p>
           
           {/* Help Message */}
           <div className="mt-4 p-4 bg-blue-900 bg-opacity-20 border border-blue-700 rounded-lg max-w-2xl mx-auto">
             <p className="text-blue-200 text-sm">
               💡 <strong>Tip:</strong> The media player includes sample audio and video files. 
               If you encounter any loading issues, try selecting a different track from the playlist below.
             </p>
             <div className="mt-3 text-xs text-blue-300 space-y-1">
               <p>🎵 <strong>Shuffle:</strong> Randomize playlist order</p>
               <p>🔁 <strong>Loop:</strong> No Loop → Loop All → Loop One</p>
               <p>📱 <strong>Background:</strong> Audio continues when app is minimized</p>
             </div>
           </div>
         </div>

        {/* Media Display */}
        <div className="bg-gray-800 rounded-lg p-6 mb-6">
          {mediaType === 'video' ? (
                         <video
               ref={videoRef}
               className="w-full rounded-lg mb-4"
               poster={currentMedia.thumbnail}
               controls={false}
               playsInline
               onError={(e) => {
                 console.error('Video loading error:', e);
                 // Handle video loading errors gracefully
               }}
               onLoadStart={() => {
                 // Reset state when new video starts loading
                 setCurrentTime(0);
                 setDuration(0);
               }}
             >
               <source src={currentMedia.url} type="video/mp4" />
               Your browser does not support the video tag.
             </video>
          ) : (
            <div className="text-center mb-4">
              <div className="w-64 h-64 mx-auto bg-gradient-to-br from-blue-600 to-purple-600 rounded-lg flex items-center justify-center mb-4">
                <svg className="w-32 h-32 text-white" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                </svg>
              </div>
            </div>
          )}

                     {/* Track Info */}
           <div className="text-center mb-6">
             <h2 className="text-2xl font-bold mb-2">{currentMedia.title}</h2>
             <p className="text-gray-400">{currentMedia.artist}</p>
             <p className="text-sm text-gray-500 mt-1">
               {mediaType === 'audio' ? 'Audio Track' : 'Video'} • {currentTrack + 1} of {currentPlaylist.length}
               {isShuffled && <span className="text-green-400 ml-2">🔀 Shuffled</span>}
               {loopMode !== 'none' && (
                 <span className="text-blue-400 ml-2">
                   {loopMode === 'all' ? '🔁 Loop All' : '🔂 Loop One'}
                 </span>
               )}
             </p>
             
             {/* Loading and Error States */}
             {isLoading && (
               <div className="mt-3 flex items-center justify-center space-x-2 text-blue-400">
                 <svg className="w-4 h-4 animate-spin" fill="currentColor" viewBox="0 0 24 24">
                   <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8z"/>
                   <path d="M12 4v2.5" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                 </svg>
                 <span className="text-sm">Loading media...</span>
               </div>
             )}
             
             {mediaError && (
               <div className="mt-3 p-3 bg-red-900 bg-opacity-20 border border-red-700 rounded-lg">
                 <div className="flex items-center space-x-2 text-red-400">
                   <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                     <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                   </svg>
                   <span className="text-sm font-medium">{mediaError}</span>
                 </div>
                 <p className="text-xs text-red-300 mt-1">
                   Try selecting a different track from the playlist below.
                 </p>
               </div>
             )}
           </div>

                     {/* Progress Bar */}
           <div className="mb-4">
             <div 
               ref={progressRef}
               className={`w-full rounded-full h-2 relative ${
                 mediaError || isLoading 
                   ? 'bg-gray-700 cursor-not-allowed' 
                   : 'bg-gray-600 cursor-pointer'
               }`}
               onClick={mediaError || isLoading ? undefined : handleSeek}
             >
               <div 
                 className="bg-blue-500 h-2 rounded-full transition-all duration-300"
                 style={{ width: `${(currentTime / duration) * 100}%` }}
               />
             </div>
             <div className="flex justify-between text-sm text-gray-400 mt-2">
               <span>{formatTime(currentTime)}</span>
               <span>{formatTime(duration)}</span>
             </div>
           </div>

                     {/* Controls */}
           <div className="flex items-center justify-center space-x-6 mb-6">
             <button
               onClick={handlePrevious}
               disabled={isLoading}
               className={`p-3 rounded-full transition-colors ${
                 isLoading 
                   ? 'bg-gray-500 cursor-not-allowed' 
                   : 'bg-gray-700 hover:bg-gray-600'
               }`}
             >
               <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24">
                 <path d="M6 6h2v12H6zm3.5 6l8.5 6V6z"/>
               </svg>
             </button>

             <button
               onClick={handlePlayPause}
               disabled={isLoading || mediaError}
               className={`p-4 rounded-full transition-colors ${
                 isLoading || mediaError 
                   ? 'bg-gray-500 cursor-not-allowed' 
                   : 'bg-blue-600 hover:bg-blue-700'
               }`}
             >
               {isLoading ? (
                 <svg className="w-8 h-8 animate-spin" fill="currentColor" viewBox="0 0 24 24">
                   <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.42 0-8-3.58-8-8s3.58-8 8-8 8 3.58 8 8-3.58 8-8 8z"/>
                   <path d="M12 4v2.5" stroke="currentColor" strokeWidth="2" strokeLinecap="round"/>
                 </svg>
               ) : isPlaying ? (
                 <svg className="w-8 h-8" fill="currentColor" viewBox="0 0 24 24">
                   <path d="M6 19h4V5H6v14zm8-14v14h4V5h-4z"/>
                 </svg>
               ) : (
                 <svg className="w-8 h-8" fill="currentColor" viewBox="0 0 24 24">
                   <path d="M8 5v14l11-7z"/>
                 </svg>
               )}
             </button>

             <button
               onClick={handleNext}
               disabled={isLoading}
               className={`p-3 rounded-full transition-colors ${
                 isLoading 
                   ? 'bg-gray-500 cursor-not-allowed' 
                   : 'bg-gray-700 hover:bg-gray-600'
               }`}
             >
               <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24">
                 <path d="M6 18l8.5-6L6 6v12zM16 6v12h2V6h-2z"/>
               </svg>
             </button>
           </div>

           {/* Shuffle and Loop Controls */}
           <div className="flex items-center justify-center space-x-4 mb-6">
             <button
               onClick={handleShuffle}
               disabled={isLoading}
               className={`p-3 rounded-lg transition-colors flex items-center space-x-2 ${
                 isLoading 
                   ? 'bg-gray-500 cursor-not-allowed' 
                   : isShuffled 
                     ? 'bg-green-600 hover:bg-green-700' 
                     : 'bg-gray-700 hover:bg-gray-600'
               }`}
               title={isShuffled ? 'Shuffle On' : 'Shuffle Off'}
             >
               <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
                 <path d="M10.59 9.17L5.41 4 4 5.41l5.17 5.17 1.42-1.41zM14.5 4l2.04 2.04L4 18.59 5.41 20 17.96 7.04 20 9.5V4h-5.5zm.33 9.41l-1.41 1.41 3.13 3.13L14.5 20H20v-5.5l-2.04 2.04-3.13-3.13z"/>
               </svg>
               <span className="text-sm font-medium">Shuffle</span>
             </button>

             <button
               onClick={handleLoopToggle}
               disabled={isLoading}
               className={`p-3 rounded-lg transition-colors flex items-center space-x-2 ${
                 isLoading 
                   ? 'bg-gray-500 cursor-not-allowed' 
                   : loopMode !== 'none' 
                     ? 'bg-blue-600 hover:bg-blue-700' 
                     : 'bg-gray-700 hover:bg-gray-600'
               }`}
               title={getLoopTooltip()}
             >
               {getLoopIcon()}
               <span className="text-sm font-medium">
                 {loopMode === 'none' ? 'No Loop' : loopMode === 'all' ? 'Loop All' : 'Loop One'}
               </span>
             </button>
           </div>

          {/* Volume Control */}
          <div className="flex items-center justify-center space-x-4">
            <svg className="w-5 h-5 text-gray-400" fill="currentColor" viewBox="0 0 24 24">
              <path d="M3 9v6h4l5 5V4L7 9H3zm13.5 3c0-1.77-1.02-3.29-2.5-4.03v8.05c1.48-.73 2.5-2.25 2.5-4.02zM14 3.23v2.06c2.89.86 5 3.54 5 6.71s-2.11 5.85-5 6.71v2.06c4.01-.91 7-4.49 7-8.77s-2.99-7.86-7-8.77z"/>
            </svg>
            <input
              type="range"
              min="0"
              max="1"
              step="0.1"
              value={volume}
              onChange={handleVolumeChange}
              className="w-24 h-2 bg-gray-600 rounded-lg appearance-none cursor-pointer"
            />
            <span className="text-sm text-gray-400 w-8">{Math.round(volume * 100)}%</span>
          </div>
        </div>

        {/* Playlist Toggle */}
        <div className="text-center mb-4">
          <button
            onClick={() => setShowPlaylist(!showPlaylist)}
            className="bg-gray-700 hover:bg-gray-600 px-6 py-2 rounded-lg transition-colors flex items-center space-x-2 mx-auto"
          >
            <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 24 24">
              <path d="M3 13h2v-2H3v2zm0 4h2v-2H3v2zm0-8h2V7H3v2zm4 4h14v-2H7v2zm0 4h14v-2H7v2zM7 7v2h14V7H7z"/>
            </svg>
            <span>{showPlaylist ? 'Hide' : 'Show'} Playlist</span>
          </button>
        </div>

                 {/* Playlist */}
         {showPlaylist && (
           <div className="bg-gray-800 rounded-lg p-6">
             <div className="flex items-center justify-between mb-4">
               <h3 className="text-xl font-bold">Playlist</h3>
               <div className="flex items-center space-x-2 text-sm">
                 {isShuffled && (
                   <span className="text-green-400 flex items-center space-x-1">
                     <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 24 24">
                       <path d="M10.59 9.17L5.41 4 4 5.41l5.17 5.17 1.42-1.41zM14.5 4l2.04 2.04L4 18.59 5.41 20 17.96 7.04 20 9.5V4h-5.5zm.33 9.41l-1.41 1.41 3.13 3.13L14.5 20H20v-5.5l-2.04 2.04-3.13-3.13z"/>
                     </svg>
                     <span>Shuffled</span>
                   </span>
                 )}
                 {loopMode !== 'none' && (
                   <span className="text-blue-400 flex items-center space-x-1">
                     {getLoopIcon()}
                     <span>{loopMode === 'all' ? 'Loop All' : 'Loop One'}</span>
                   </span>
                 )}
               </div>
             </div>
             <div className="space-y-2">
               {currentPlaylist.map((track, index) => (
                <div
                  key={track.id}
                                     onClick={() => {
                     // Pause current media before switching
                     const currentMediaElement = mediaType === 'audio' ? audioRef.current : videoRef.current;
                     if (currentMediaElement) {
                       currentMediaElement.pause();
                     }
                     
                     setCurrentTrack(index);
                     setMediaType(track.type);
                     setIsPlaying(false);
                     setCurrentTime(0);
                   }}
                  className={`flex items-center space-x-4 p-3 rounded-lg cursor-pointer transition-colors ${
                    index === currentTrack 
                      ? 'bg-blue-600 text-white' 
                      : 'bg-gray-700 hover:bg-gray-600'
                  }`}
                >
                  <div className="w-12 h-12 bg-gray-600 rounded-lg flex items-center justify-center">
                    {track.type === 'audio' ? (
                      <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
                      </svg>
                    ) : (
                      <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24">
                        <path d="M17 10.5V7c0-.55-.45-1-1-1H4c-.55 0-1 .45-1 1v10c0 .55.45 1 1 1h12c.55 0 1-.45 1-1v-3.5l4 4v-11l-4 4z"/>
                      </svg>
                    )}
                  </div>
                  <div className="flex-1">
                    <h4 className="font-medium">{track.title}</h4>
                    <p className="text-sm text-gray-400">{track.artist}</p>
                  </div>
                  <div className="text-sm text-gray-400">
                    {track.type === 'audio' ? 'Audio' : 'Video'}
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Hidden Audio Element for Background Playback */}
        <audio
          ref={audioRef}
          src={mediaType === 'audio' ? currentMedia.url : ''}
          preload="metadata"
          playsInline
          webkit-playsinline="true"
          onError={(e) => {
            console.error('Audio loading error:', e);
            // Handle audio loading errors gracefully
          }}
          onLoadStart={() => {
            // Reset state when new audio starts loading
            setCurrentTime(0);
            setDuration(0);
          }}
        />

        {/* PWA Background Audio Info */}
        <div className="mt-8 text-center">
          <div className="bg-blue-900 bg-opacity-20 border border-blue-700 rounded-lg p-4">
            <h3 className="text-lg font-semibold mb-2">🎵 Background Audio Support</h3>
            <p className="text-gray-300 text-sm">
              This PWA supports background audio playback on mobile devices. 
              You can continue listening to music while using other apps or when the screen is locked.
            </p>
            <div className="mt-3 text-xs text-gray-400">
              <p>• Audio continues playing in background</p>
              <p>• Media controls in notification panel</p>
              <p>• Works with screen locked</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MediaPlayer;
