import { useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

const STORAGE_KEY = 'lastVisitedRoute';
const DEFAULT_ROUTE = '/';

// Define routes that should be saved for persistence
const PERSISTABLE_ROUTES = [
  '/',
  '/media',
  '/games/mines',
  '/games/2048',
  '/tools/qr-generation',
  '/tools/encoding',
  '/dashboard',
  '/users'
];

// Define routes that require authentication
const PROTECTED_ROUTES = [
  '/dashboard',
  '/users'
];

/**
 * Custom hook to handle route persistence in PWA
 * Saves the current route to localStorage and restores it on app startup
 */
export const useRoutePersistence = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const { isAuthenticated, loading } = useAuth();

  // Save current route to localStorage when route changes
  useEffect(() => {
    const currentPath = location.pathname;
    
    // Only save routes that are in our persistable list
    if (PERSISTABLE_ROUTES.includes(currentPath)) {
      localStorage.setItem(STORAGE_KEY, currentPath);
    }
  }, [location.pathname]);

  // Restore last visited route on app startup
  useEffect(() => {
    if (!loading) {
      const savedRoute = localStorage.getItem(STORAGE_KEY);
      
      if (savedRoute && savedRoute !== DEFAULT_ROUTE) {
        // Check if the saved route requires authentication
        const isProtectedRoute = PROTECTED_ROUTES.includes(savedRoute);
        
        // If it's a protected route and user is not authenticated, don't navigate
        if (isProtectedRoute && !isAuthenticated) {
          // Clear the saved route and stay on current page
          localStorage.setItem(STORAGE_KEY, DEFAULT_ROUTE);
          return;
        }
        
        // If we're currently on the default route, navigate to saved route
        if (location.pathname === DEFAULT_ROUTE) {
          navigate(savedRoute, { replace: true });
        }
      }
    }
  }, [isAuthenticated, loading, navigate, location.pathname]);

  // Function to clear saved route (useful for logout)
  const clearSavedRoute = () => {
    localStorage.setItem(STORAGE_KEY, DEFAULT_ROUTE);
  };

  // Function to get the last saved route
  const getLastSavedRoute = () => {
    return localStorage.getItem(STORAGE_KEY) || DEFAULT_ROUTE;
  };

  return {
    clearSavedRoute,
    getLastSavedRoute
  };
};
