import React, { createContext, useContext, useState, useEffect } from 'react';
import apiService from '../services/apiService';
import oidcService from '../services/oidcService';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // Hydrate from localStorage set by OIDC callback
    try {
      const token = localStorage.getItem('token');
      const storedUser = apiService.getCurrentUser();
      if (token && storedUser) {
        setUser(storedUser);
      }
    } catch (err) {
      console.error('Auth hydration failed:', err);
      apiService.logout();
    } finally {
      setLoading(false);
    }
  }, []);

  const login = async (returnTo = '/dashboard') => {
    setError(null);
    setLoading(true);
    try {
      await oidcService.startLogin(returnTo);
    } catch (err) {
      setError(err.message);
      setLoading(false);
      throw err;
    }
  };

  const register = async (userData) => {
    try {
      setError(null);
      setLoading(true);
      
      const response = await apiService.register(userData);
      return response;
    } catch (err) {
      setError(err.message);
      throw err;
    } finally {
      setLoading(false);
    }
  };

  const logout = async () => {
    try {
      await oidcService.logoutOidc();
      setUser(null);
      setError(null);
    } catch (err) {
      console.error('Logout error:', err);
      // Force logout even if API call fails
      setUser(null);
      setError(null);
    }
  };

  const updateProfile = async (profileData) => {
    try {
      setError(null);
      const response = await apiService.updateUserProfile(profileData);
      
      // Update local user state
      if (response.success) {
        setUser(prevUser => ({ ...prevUser, ...profileData }));
      }
      
      return response;
    } catch (err) {
      setError(err.message);
      throw err;
    }
  };

  const clearError = () => {
    setError(null);
  };

  const value = {
    user,
    loading,
    error,
    login,
    register,
    logout,
    updateProfile,
    clearError,
    isAuthenticated: !!user,
    // Allow OIDC callback to atomically set the session
    completeOidcLogin: (authenticatedUser) => {
      setUser(authenticatedUser);
      setError(null);
      setLoading(false);
    },
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};
