import React, { useState, useRef, useEffect } from 'react';
import apiService from '../services/apiService';

const StateMachine = () => {
  const canvasRef = useRef(null);
  const [blocks, setBlocks] = useState([]);
  const [connections, setConnections] = useState([]);
  const [selectedBlock, setSelectedBlock] = useState(null);
  const [draggingBlock, setDraggingBlock] = useState(null);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [connectingFrom, setConnectingFrom] = useState(null);
  const [hoveredBlock, setHoveredBlock] = useState(null);
  const [editingBlock, setEditingBlock] = useState(null);
  const [editingConnection, setEditingConnection] = useState(null);
  const [blockNameInput, setBlockNameInput] = useState('');
  const [connectionNameInput, setConnectionNameInput] = useState('');
  const [nextId, setNextId] = useState(1);
  const [showInstructions, setShowInstructions] = useState(false);
  const [showSavedIndicator, setShowSavedIndicator] = useState(false);
  const [showClearConfirm, setShowClearConfirm] = useState(false);
  const [editingConnectionShape, setEditingConnectionShape] = useState(null);
  const [draggingControlPoint, setDraggingControlPoint] = useState(null);
  const [editingDescription, setEditingDescription] = useState(null);
  const [descriptionInput, setDescriptionInput] = useState('');
  const [editingCode, setEditingCode] = useState(null);
  const [codeInput, setCodeInput] = useState('');
  const [canvasSize, setCanvasSize] = useState({ width: 1200, height: 800 });
  const [canvasOffset, setCanvasOffset] = useState({ x: 0, y: 0 });
  const [isPanning, setIsPanning] = useState(false);
  const [lastPanPoint, setLastPanPoint] = useState({ x: 0, y: 0 });
  const [machines, setMachines] = useState([]);
  const [selectedMachine, setSelectedMachine] = useState('');
  const [isLoadingMachines, setIsLoadingMachines] = useState(false);

  // Load machines from backend
  useEffect(() => {
    const fetchMachines = async () => {
      setIsLoadingMachines(true);
      try {
        const res = await apiService.getAllStateMachines();
        const items = Array.isArray(res?.data) ? res.data : [];
        setMachines(items);
        if (items.length > 0 && !selectedMachine) {
          setSelectedMachine(items[0].name);
        }
      } catch (e) {
        console.error('Failed to load machines', e);
      } finally {
        setIsLoadingMachines(false);
      }
    };
    fetchMachines();
  }, []);

  // Block types
  const BLOCK_TYPES = {
    EVENT: 'event',
    COMMAND: 'command'
  };

  // LocalStorage key
  const STORAGE_KEY = 'stateMachineData';

  // Load data from localStorage on mount
  useEffect(() => {
    try {
      const savedData = localStorage.getItem(STORAGE_KEY);
      if (savedData) {
        const { blocks: savedBlocks, connections: savedConnections, nextId: savedNextId } = JSON.parse(savedData);
        if (savedBlocks) setBlocks(savedBlocks);
        if (savedConnections) setConnections(savedConnections);
        if (savedNextId) setNextId(savedNextId);
      }
    } catch (error) {
      console.error('Failed to load state machine data from localStorage:', error);
    }
  }, []);

  // Save data to localStorage whenever blocks, connections, or nextId changes
  useEffect(() => {
    // Skip saving on initial mount (when blocks/connections are empty from initial state)
    if (blocks.length === 0 && connections.length === 0 && nextId === 1) {
      return;
    }

    try {
      const dataToSave = {
        blocks,
        connections,
        nextId
      };
      localStorage.setItem(STORAGE_KEY, JSON.stringify(dataToSave));
      
      // Show saved indicator
      setShowSavedIndicator(true);
      const timer = setTimeout(() => {
        setShowSavedIndicator(false);
      }, 1500);
      
      return () => clearTimeout(timer);
    } catch (error) {
      console.error('Failed to save state machine data to localStorage:', error);
    }
  }, [blocks, connections, nextId]);

  // Add new block to canvas
  const addBlock = (type) => {
    const newBlock = {
      id: nextId,
      type: type,
      name: type === BLOCK_TYPES.EVENT ? `Event ${nextId}` : `Command ${nextId}`,
      x: 100 + (nextId * 20), // Offset each new block
      y: 100 + (nextId * 20),
      width: type === BLOCK_TYPES.EVENT ? 120 : 120,
      height: type === BLOCK_TYPES.EVENT ? 80 : 60,
      description: '',
      code: ''
    };
    setBlocks([...blocks, newBlock]);
    setNextId(nextId + 1);
  };

  // Handle mouse down on block - start dragging
  const handleBlockMouseDown = (e, block) => {
    e.stopPropagation();
    const rect = canvasRef.current.getBoundingClientRect();
    const offsetX = e.clientX - rect.left - block.x;
    const offsetY = e.clientY - rect.top - block.y;
    setDragOffset({ x: offsetX, y: offsetY });
    setDraggingBlock(block);
    setSelectedBlock(block);
  };

  // Handle mouse move - drag block, control point, or pan canvas
  const handleMouseMove = (e) => {
    if (draggingBlock) {
      const rect = canvasRef.current.getBoundingClientRect();
      const newX = e.clientX - rect.left - dragOffset.x;
      const newY = e.clientY - rect.top - dragOffset.y;

      setBlocks(blocks.map(b => 
        b.id === draggingBlock.id 
          ? { ...b, x: Math.max(0, newX), y: Math.max(0, newY) }
          : b
      ));
    } else if (draggingControlPoint) {
      const rect = canvasRef.current.getBoundingClientRect();
      const newX = e.clientX - rect.left;
      const newY = e.clientY - rect.top;
      
      updateControlPoint(
        draggingControlPoint.connection,
        draggingControlPoint.index,
        newX,
        newY
      );
    } else if (isPanning) {
      const deltaX = e.clientX - lastPanPoint.x;
      const deltaY = e.clientY - lastPanPoint.y;
      setCanvasOffset(prev => ({
        x: prev.x + deltaX,
        y: prev.y + deltaY
      }));
      setLastPanPoint({ x: e.clientX, y: e.clientY });
    }
  };

  // Handle mouse up - stop dragging
  const handleMouseUp = () => {
    setDraggingBlock(null);
    setDraggingControlPoint(null);
    setIsPanning(false);
  };

  // Start connection from a block
  const startConnection = (e, fromBlock) => {
    e.stopPropagation();
    e.preventDefault(); // Prevent context menu on mobile
    setConnectingFrom(fromBlock);
  };

  // Handle touch/long press for mobile
  const handleConnectionButtonClick = (e, block) => {
    e.stopPropagation();
    e.preventDefault();
    if (connectingFrom && connectingFrom.id === block.id) {
      // Cancel connection if clicking the same block
      cancelConnection();
    } else {
      startConnection(e, block);
    }
  };

  // Complete connection to a block
  const completeConnection = (e, toBlock) => {
    e.stopPropagation();
    if (connectingFrom && connectingFrom.id !== toBlock.id) {
      // Allow multiple connections from A to B (directional)
      const newConnection = {
        id: `conn-${connectingFrom.id}-${toBlock.id}-${Date.now()}`,
        from: connectingFrom.id,
        to: toBlock.id,
        name: '',
        timestamp: Date.now(), // For uniqueness
        controlPoints: [] // Array to store custom control points
      };
      setConnections([...connections, newConnection]);
      setConnectingFrom(null);
    }
  };

  // Cancel connection
  const cancelConnection = () => {
    setConnectingFrom(null);
  };

  // Clear all blocks and connections
  const clearAll = () => {
    setShowClearConfirm(true);
  };

  // Confirm clear all
  const confirmClearAll = () => {
    setBlocks([]);
    setConnections([]);
    setSelectedBlock(null);
    setConnectingFrom(null);
    setNextId(1);
    setShowClearConfirm(false);
  };

  // Delete selected block
  const deleteBlock = () => {
    if (selectedBlock) {
      setBlocks(blocks.filter(b => b.id !== selectedBlock.id));
      // Also remove connections involving this block
      setConnections(connections.filter(c => 
        c.from !== selectedBlock.id && c.to !== selectedBlock.id
      ));
      setSelectedBlock(null);
    }
  };

  // Delete connection
  const deleteConnection = (connectionId) => {
    setConnections(connections.filter(c => c.id !== connectionId));
    setEditingConnection(null);
  };

  // Edit block name
  const startEditingBlock = (block) => {
    setEditingBlock(block);
    setBlockNameInput(block.name);
  };

  // Save block name
  const saveBlockName = () => {
    if (editingBlock && blockNameInput.trim()) {
      setBlocks(blocks.map(b => 
        b.id === editingBlock.id 
          ? { ...b, name: blockNameInput.trim() }
          : b
      ));
      setEditingBlock(null);
      setBlockNameInput('');
    }
  };

  // Edit connection name
  const startEditingConnection = (connection) => {
    setEditingConnection(connection);
    setConnectionNameInput(connection.name);
  };

  // Save connection name
  const saveConnectionName = () => {
    if (editingConnection) {
      setConnections(connections.map(c => 
        c.id === editingConnection.id 
          ? { ...c, name: connectionNameInput.trim() }
          : c
      ));
      setEditingConnection(null);
      setConnectionNameInput('');
    }
  };

  // Add control point to connection
  const addControlPoint = (connection, x, y) => {
    setConnections(connections.map(c => 
      c.id === connection.id 
        ? { ...c, controlPoints: [...(c.controlPoints || []), { x, y }] }
        : c
    ));
  };

  // Update control point position
  const updateControlPoint = (connection, pointIndex, x, y) => {
    setConnections(connections.map(c => 
      c.id === connection.id 
        ? { 
            ...c, 
            controlPoints: (c.controlPoints || []).map((point, index) => 
              index === pointIndex ? { x, y } : point
            )
          }
        : c
    ));
  };

  // Remove control point
  const removeControlPoint = (connection, pointIndex) => {
    setConnections(connections.map(c => 
      c.id === connection.id 
        ? { 
            ...c, 
            controlPoints: (c.controlPoints || []).filter((_, index) => index !== pointIndex)
          }
        : c
    ));
  };

  // Start editing block description
  const startEditingDescription = (block) => {
    setEditingDescription(block);
    setDescriptionInput(block.description || '');
  };

  // Save block description
  const saveDescription = () => {
    if (editingDescription) {
      setBlocks(blocks.map(b => 
        b.id === editingDescription.id 
          ? { ...b, description: descriptionInput.trim() }
          : b
      ));
      setEditingDescription(null);
      setDescriptionInput('');
    }
  };

  // Start editing block code
  const startEditingCode = (block) => {
    setEditingCode(block);
    setCodeInput(block.code || '');
  };

  // Save block code
  const saveCode = () => {
    if (editingCode) {
      setBlocks(blocks.map(b => 
        b.id === editingCode.id 
          ? { ...b, code: codeInput.trim() }
          : b
      ));
      setEditingCode(null);
      setCodeInput('');
    }
  };

  // Handle canvas panning
  const handleCanvasMouseDown = (e) => {
    if (e.target === e.currentTarget || e.target.tagName === 'svg') {
      setIsPanning(true);
      setLastPanPoint({ x: e.clientX, y: e.clientY });
    }
  };

  const handleCanvasMouseMove = (e) => {
    if (isPanning) {
      const deltaX = e.clientX - lastPanPoint.x;
      const deltaY = e.clientY - lastPanPoint.y;
      setCanvasOffset(prev => ({
        x: prev.x + deltaX,
        y: prev.y + deltaY
      }));
      setLastPanPoint({ x: e.clientX, y: e.clientY });
    }
  };

  const handleCanvasMouseUp = () => {
    setIsPanning(false);
  };

  // Expand canvas to fit all blocks
  const expandCanvasToFit = () => {
    if (blocks.length === 0) return;
    
    const minX = Math.min(...blocks.map(b => b.x)) - 100;
    const maxX = Math.max(...blocks.map(b => b.x + b.width)) + 100;
    const minY = Math.min(...blocks.map(b => b.y)) - 100;
    const maxY = Math.max(...blocks.map(b => b.y + b.height)) + 100;
    
    const newWidth = Math.max(1200, maxX - minX);
    const newHeight = Math.max(800, maxY - minY);
    
    setCanvasSize({ width: newWidth, height: newHeight });
    
    // Center the view on the content
    setCanvasOffset({ 
      x: (window.innerWidth - newWidth) / 2, 
      y: (window.innerHeight - newHeight) / 2 
    });
  };

  // Reset canvas view
  const resetCanvasView = () => {
    setCanvasOffset({ x: 0, y: 0 });
    setCanvasSize({ width: 1200, height: 800 });
  };

  // Get block center point for connections
  const getBlockCenter = (block) => {
    return {
      x: block.x + block.width / 2,
      y: block.y + block.height / 2
    };
  };

  // Get connection point on block border
  const getConnectionPoint = (fromBlock, toBlock) => {
    const fromCenter = getBlockCenter(fromBlock);
    const toCenter = getBlockCenter(toBlock);
    
    // Calculate direction vector
    const dx = toCenter.x - fromCenter.x;
    const dy = toCenter.y - fromCenter.y;
    const length = Math.sqrt(dx * dx + dy * dy);
    
    if (length === 0) return { from: fromCenter, to: toCenter };
    
    // Normalize direction
    const dirX = dx / length;
    const dirY = dy / length;
    
    // Calculate intersection points on block borders
    const fromX = fromCenter.x + dirX * (fromBlock.width / 2);
    const fromY = fromCenter.y + dirY * (fromBlock.height / 2);
    
    const toX = toCenter.x - dirX * (toBlock.width / 2);
    const toY = toCenter.y - dirY * (toBlock.height / 2);
    
    return {
      from: { x: fromX, y: fromY },
      to: { x: toX, y: toY }
    };
  };

  // Draw connection line with curve for multiple connections
  const drawConnection = (connection, connectionIndex) => {
    const fromBlock = blocks.find(b => b.id === connection.from);
    const toBlock = blocks.find(b => b.id === connection.to);
    
    if (!fromBlock || !toBlock) return null;

    // Get connection points on block borders
    const connectionPoints = getConnectionPoint(fromBlock, toBlock);
    const from = connectionPoints.from;
    const to = connectionPoints.to;

    // Check if connection has custom control points
    const hasCustomControlPoints = connection.controlPoints && connection.controlPoints.length > 0;
    
    let pathD;
    let labelX, labelY;

    if (hasCustomControlPoints) {
      // Use custom control points for path
      const points = [from, ...connection.controlPoints, to];
      
      // Adjust end point to account for arrow
      const arrowOffset = 20;
      const lastControlPoint = connection.controlPoints[connection.controlPoints.length - 1] || from;
      const angle = Math.atan2(to.y - lastControlPoint.y, to.x - lastControlPoint.x);
      const endX = to.x - Math.cos(angle) * arrowOffset;
      const endY = to.y - Math.sin(angle) * arrowOffset;
      
      // Create path with multiple control points
      let path = `M ${from.x} ${from.y}`;
      if (connection.controlPoints.length === 1) {
        // Single control point - quadratic curve
        path += ` Q ${connection.controlPoints[0].x} ${connection.controlPoints[0].y} ${endX} ${endY}`;
        labelX = (from.x + 2 * connection.controlPoints[0].x + endX) / 4;
        labelY = (from.y + 2 * connection.controlPoints[0].y + endY) / 4;
      } else {
        // Multiple control points - cubic curves
        for (let i = 0; i < connection.controlPoints.length; i += 2) {
          const cp1 = connection.controlPoints[i];
          const cp2 = connection.controlPoints[i + 1] || connection.controlPoints[i];
          const end = i + 2 >= connection.controlPoints.length ? 
            { x: endX, y: endY } : 
            connection.controlPoints[i + 1] || { x: endX, y: endY };
          
          path += ` C ${cp1.x} ${cp1.y} ${cp2.x} ${cp2.y} ${end.x} ${end.y}`;
        }
        
        // Calculate label position at midpoint of path
        const midIndex = Math.floor(connection.controlPoints.length / 2);
        const midControlPoint = connection.controlPoints[midIndex];
        labelX = midControlPoint.x;
        labelY = midControlPoint.y;
      }
      pathD = path;
    } else {
      // Use automatic curve calculation for multiple connections
      const sameDirectionConnections = connections.filter(c => 
        c.from === connection.from && c.to === connection.to
      );
      
      const indexInGroup = sameDirectionConnections.findIndex(c => c.id === connection.id);
      const totalInGroup = sameDirectionConnections.length;

      let curveOffset = 0;
      if (totalInGroup > 1) {
        const spacing = 50;
        curveOffset = (indexInGroup - (totalInGroup - 1) / 2) * spacing;
      }

      const dx = to.x - from.x;
      const dy = to.y - from.y;
      const length = Math.sqrt(dx * dx + dy * dy);
      
      if (length === 0) return null;

      const perpX = -dy / length;
      const perpY = dx / length;

      const midX = (from.x + to.x) / 2 + perpX * curveOffset;
      const midY = (from.y + to.y) / 2 + perpY * curveOffset;

      labelX = (from.x + 2 * midX + to.x) / 4;
      labelY = (from.y + 2 * midY + to.y) / 4;

      const arrowOffset = 20;
      const angle = Math.atan2(to.y - midY, to.x - midX);
      const endX = to.x - Math.cos(angle) * arrowOffset;
      const endY = to.y - Math.sin(angle) * arrowOffset;

      pathD = `M ${from.x} ${from.y} Q ${midX} ${midY} ${endX} ${endY}`;
    }

    return (
      <g key={connection.id}>
        {/* Main connection path */}
        <path
          d={pathD}
          stroke="#2563eb"
          strokeWidth="3"
          fill="none"
          markerEnd="url(#arrowhead)"
          onDoubleClick={(e) => {
            e.stopPropagation();
            const rect = canvasRef.current.getBoundingClientRect();
            const x = e.clientX - rect.left;
            const y = e.clientY - rect.top;
            addControlPoint(connection, x, y);
          }}
        />
        
        {/* Control points for custom connections */}
        {hasCustomControlPoints && connection.controlPoints.map((point, index) => (
          <g key={`control-${connection.id}-${index}`}>
            <circle
              cx={point.x}
              cy={point.y}
              r="6"
              fill="#f59e0b"
              stroke="#ffffff"
              strokeWidth="2"
              onMouseDown={(e) => {
                e.stopPropagation();
                setDraggingControlPoint({ connection, index });
              }}
              style={{ cursor: 'grab' }}
            />
            <circle
              cx={point.x}
              cy={point.y}
              r="4"
              fill="#ffffff"
              opacity="0.8"
            />
            <text
              x={point.x}
              y={point.y + 2}
              textAnchor="middle"
              fontSize="10"
              fontWeight="bold"
              fill="#f59e0b"
              pointerEvents="none"
            >
              {index + 1}
            </text>
          </g>
        ))}
        
        {/* Connection label background */}
        {connection.name ? (
          <>
            <rect
              x={labelX - 40}
              y={labelY - 12}
              width="80"
              height="24"
              fill="white"
              stroke="#3b82f6"
              strokeWidth="1"
              rx="3"
              onClick={() => startEditingConnection(connection)}
              style={{ cursor: 'pointer' }}
            />
            <text
              x={labelX}
              y={labelY + 5}
              textAnchor="middle"
              fontSize="12"
              fontWeight="500"
              fill="#1e40af"
              onClick={() => startEditingConnection(connection)}
              style={{ cursor: 'pointer' }}
            >
              {connection.name}
            </text>
          </>
        ) : (
          /* Clickable circle for unnamed connections */
          <circle
            cx={labelX}
            cy={labelY}
            r="8"
            fill="#3b82f6"
            stroke="white"
            strokeWidth="2"
            onClick={() => startEditingConnection(connection)}
            style={{ cursor: 'pointer' }}
          />
        )}
      </g>
    );
  };

  // Listen for Escape key to cancel connection
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.key === 'Escape') {
        cancelConnection();
        setEditingBlock(null);
        setEditingConnection(null);
        setShowInstructions(false);
        setShowClearConfirm(false);
      }
      if (e.key === 'Delete' && selectedBlock && !editingBlock) {
        deleteBlock();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedBlock, editingBlock]);

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white shadow rounded-lg">
          <div className="px-4 py-5 sm:p-6">
            {/* Header */}
            <div className="mb-6 flex justify-between items-start">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Perfect World - State Machine</h1>
                <p className="text-gray-600 mt-2">
                  Design state machines by dragging blocks and connecting them.
                </p>
              </div>
              
              {/* Auto-save indicator */}
              <div className="flex items-center space-x-2">
                {showSavedIndicator && (
                  <div className="flex items-center space-x-2 bg-green-50 border border-green-200 text-green-700 px-3 py-1 rounded-md text-sm animate-fade-in">
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                    </svg>
                    <span>Saved</span>
                  </div>
                )}
              </div>
            </div>

            {/* Responsive Toolbar */}
            <div className="mb-4 pb-4 border-b border-gray-200">
              {/* Mobile/Tablet View - Stacked Layout */}
              <div className="block lg:hidden space-y-3">
                {/* Top Row - Block Creation */}
                <div className="flex flex-wrap gap-2">
                  <button
                    onClick={() => addBlock(BLOCK_TYPES.EVENT)}
                    className="bg-purple-600 hover:bg-purple-700 text-white px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2 flex-1 min-w-0"
                  >
                    <svg className="w-4 h-4 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                    </svg>
                    <span className="truncate">Add Event</span>
                  </button>
                  <button
                    onClick={() => addBlock(BLOCK_TYPES.COMMAND)}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2 flex-1 min-w-0"
                  >
                    <svg className="w-4 h-4 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                    </svg>
                    <span className="truncate">Add Command</span>
                  </button>
                  {selectedBlock && (
                    <button
                      onClick={deleteBlock}
                      className="bg-red-600 hover:bg-red-700 text-white px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                    >
                      <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                      </svg>
                      <span className="hidden sm:inline">Delete</span>
                    </button>
                  )}
                </div>

                {/* Machine Selection Row */}
                <div className="flex flex-wrap gap-2 items-center">
                  <div className="flex items-center space-x-2 min-w-0 flex-1">
                    <label className="text-xs text-gray-600 whitespace-nowrap">Machine:</label>
                    <select
                      value={selectedMachine}
                      onChange={(e) => setSelectedMachine(e.target.value)}
                      className="border border-gray-300 rounded-md text-sm px-2 py-1 flex-1 min-w-0"
                      disabled={isLoadingMachines || machines.length === 0}
                    >
                      {machines.map(m => (
                        <option key={m.name} value={m.name}>{m.name}</option>
                      ))}
                    </select>
                  </div>
                  <button
                    onClick={async () => {
                      const item = machines.find(m => m.name === selectedMachine);
                      if (!item) return;
                      try {
                        const parsed = item.value ? JSON.parse(item.value) : { blocks: [], connections: [], nextId: 1 };
                        setBlocks(parsed.blocks || []);
                        setConnections(parsed.connections || []);
                        setNextId(parsed.nextId || 1);
                      } catch (e) {
                        console.error('Invalid machine JSON', e);
                      }
                    }}
                    className="bg-indigo-600 hover:bg-indigo-700 text-white px-3 py-2 rounded-md text-sm"
                    disabled={!selectedMachine}
                  >
                    Load
                  </button>
                </div>

                {/* Action Buttons Row */}
                <div className="flex flex-wrap gap-2">
                  <button
                    onClick={async () => {
                      if (!selectedMachine) return;
                      const payload = JSON.stringify({ blocks, connections, nextId });
                      try {
                        await apiService.updateStateMachine(selectedMachine, payload);
                        const res = await apiService.getAllStateMachines();
                        setMachines(Array.isArray(res?.data) ? res.data : []);
                      } catch (e) {
                        console.error('Failed to update machine', e);
                      }
                    }}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-2 rounded-md text-sm"
                    disabled={!selectedMachine}
                  >
                    Update
                  </button>
                  <button
                    onClick={async () => {
                      const name = prompt('Enter new machine name');
                      if (!name) return;
                      const payload = JSON.stringify({ blocks, connections, nextId });
                      try {
                        await apiService.insertStateMachine(name, payload);
                        const res = await apiService.getAllStateMachines();
                        const items = Array.isArray(res?.data) ? res.data : [];
                        setMachines(items);
                        setSelectedMachine(name);
                      } catch (e) {
                        console.error('Failed to insert machine', e);
                      }
                    }}
                    className="bg-green-600 hover:bg-green-700 text-white px-3 py-2 rounded-md text-sm"
                  >
                    Save As New
                  </button>
                </div>

                {/* Canvas Control Row */}
                <div className="flex flex-wrap gap-2">
                  <button
                    onClick={expandCanvasToFit}
                    className="bg-green-600 hover:bg-green-700 text-white px-3 py-2 rounded-md text-sm transition-colors flex items-center space-x-2"
                    title="Expand canvas to fit all blocks"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 8V4m0 0h4M4 4l5 5m11-1V4m0 0h-4m4 0l-5 5M4 16v4m0 0h4m-4 0l5-5m11 5l-5-5m5 5v-4m0 4h-4" />
                    </svg>
                    <span className="hidden sm:inline">Fit to Content</span>
                    <span className="sm:hidden">Fit</span>
                  </button>
                  <button
                    onClick={resetCanvasView}
                    className="bg-gray-600 hover:bg-gray-700 text-white px-3 py-2 rounded-md text-sm transition-colors flex items-center space-x-2"
                    title="Reset canvas view"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                    </svg>
                    <span className="hidden sm:inline">Reset View</span>
                    <span className="sm:hidden">Reset</span>
                  </button>
                  <button
                    onClick={clearAll}
                    className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-3 py-2 rounded-md text-sm transition-colors flex items-center space-x-2"
                    title="Clear all blocks and connections"
                  >
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                    </svg>
                    <span className="hidden sm:inline">Clear All</span>
                    <span className="sm:hidden">Clear</span>
                  </button>
                  <button
                    onClick={() => setShowInstructions(true)}
                    className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-3 py-2 rounded-md text-sm transition-colors flex items-center space-x-2"
                    title="Show instructions"
                  >
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-3a1 1 0 00-.867.5 1 1 0 11-1.731-1A3 3 0 0113 8a3.001 3.001 0 01-2 2.83V11a1 1 0 11-2 0v-1a1 1 0 011-1 1 1 0 100-2zm0 8a1 1 0 100-2 1 1 0 000 2z" clipRule="evenodd" />
                    </svg>
                    <span className="hidden sm:inline">Help</span>
                    <span className="sm:hidden">?</span>
                  </button>
                </div>
              </div>

              {/* Desktop View - Horizontal Layout */}
              <div className="hidden lg:flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <button
                    onClick={() => addBlock(BLOCK_TYPES.EVENT)}
                    className="bg-purple-600 hover:bg-purple-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                  >
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                    </svg>
                    <span>Add Event (Oval)</span>
                  </button>
                  <button
                    onClick={() => addBlock(BLOCK_TYPES.COMMAND)}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                  >
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                    </svg>
                    <span>Add Command (Rectangle)</span>
                  </button>
                  {selectedBlock && (
                    <button
                      onClick={deleteBlock}
                      className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                    >
                      <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                      </svg>
                      <span>Delete Selected</span>
                    </button>
                  )}
                </div>

                {/* Right side buttons */}
                <div className="flex items-center space-x-3">
                  {/* Machine selector */}
                  <div className="flex items-center space-x-2">
                    <label className="text-sm text-gray-600">Machine:</label>
                    <select
                      value={selectedMachine}
                      onChange={(e) => setSelectedMachine(e.target.value)}
                      className="border border-gray-300 rounded-md text-sm px-2 py-1"
                      disabled={isLoadingMachines || machines.length === 0}
                    >
                      {machines.map(m => (
                        <option key={m.name} value={m.name}>{m.name}</option>
                      ))}
                    </select>
                    <button
                      onClick={async () => {
                        const item = machines.find(m => m.name === selectedMachine);
                        if (!item) return;
                        try {
                          const parsed = item.value ? JSON.parse(item.value) : { blocks: [], connections: [], nextId: 1 };
                          setBlocks(parsed.blocks || []);
                          setConnections(parsed.connections || []);
                          setNextId(parsed.nextId || 1);
                        } catch (e) {
                          console.error('Invalid machine JSON', e);
                        }
                      }}
                      className="bg-indigo-600 hover:bg-indigo-700 text-white px-3 py-2 rounded-md text-sm"
                      disabled={!selectedMachine}
                    >
                      Load
                    </button>
                  </div>
                  
                  {/* Save/Update buttons */}
                  <button
                    onClick={async () => {
                      if (!selectedMachine) return;
                      const payload = JSON.stringify({ blocks, connections, nextId });
                      try {
                        await apiService.updateStateMachine(selectedMachine, payload);
                        const res = await apiService.getAllStateMachines();
                        setMachines(Array.isArray(res?.data) ? res.data : []);
                      } catch (e) {
                        console.error('Failed to update machine', e);
                      }
                    }}
                    className="bg-blue-600 hover:bg-blue-700 text-white px-3 py-2 rounded-md text-sm"
                    disabled={!selectedMachine}
                  >
                    Update
                  </button>
                  <button
                    onClick={async () => {
                      const name = prompt('Enter new machine name');
                      if (!name) return;
                      const payload = JSON.stringify({ blocks, connections, nextId });
                      try {
                        await apiService.insertStateMachine(name, payload);
                        const res = await apiService.getAllStateMachines();
                        const items = Array.isArray(res?.data) ? res.data : [];
                        setMachines(items);
                        setSelectedMachine(name);
                      } catch (e) {
                        console.error('Failed to insert machine', e);
                      }
                    }}
                    className="bg-green-600 hover:bg-green-700 text-white px-3 py-2 rounded-md text-sm"
                  >
                    Save As New
                  </button>
                  <button
                    onClick={expandCanvasToFit}
                    className="bg-green-600 hover:bg-green-700 text-white px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                    title="Expand canvas to fit all blocks"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 8V4m0 0h4M4 4l5 5m11-1V4m0 0h-4m4 0l-5 5M4 16v4m0 0h4m-4 0l5-5m11 5l-5-5m5 5v-4m0 4h-4" />
                    </svg>
                    <span>Fit to Content</span>
                  </button>
                  <button
                    onClick={resetCanvasView}
                    className="bg-gray-600 hover:bg-gray-700 text-white px-3 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                    title="Reset canvas view"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                    </svg>
                    <span>Reset View</span>
                  </button>
                  <button
                    onClick={clearAll}
                    className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                    title="Clear all blocks and connections"
                  >
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z" clipRule="evenodd" />
                    </svg>
                    <span>Clear All</span>
                  </button>
                  <button
                    onClick={() => setShowInstructions(true)}
                    className="bg-gray-100 hover:bg-gray-200 text-gray-700 px-4 py-2 rounded-md text-sm font-medium transition-colors flex items-center space-x-2"
                    title="Show instructions"
                  >
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-3a1 1 0 00-.867.5 1 1 0 11-1.731-1A3 3 0 0113 8a3.001 3.001 0 01-2 2.83V11a1 1 0 11-2 0v-1a1 1 0 011-1 1 1 0 100-2zm0 8a1 1 0 100-2 1 1 0 000 2z" clipRule="evenodd" />
                    </svg>
                    <span>Help</span>
                  </button>
                </div>
              </div>
            </div>

            {/* Canvas */}
            <div 
              className="border-2 border-gray-300 rounded-lg bg-gray-50 overflow-hidden relative" 
              style={{ 
                height: '600px',
                cursor: isPanning ? 'grabbing' : 'grab'
              }}
              onMouseDown={handleCanvasMouseDown}
              onMouseMove={handleCanvasMouseMove}
              onMouseUp={handleCanvasMouseUp}
            >
              <svg
                ref={canvasRef}
                width={canvasSize.width}
                height={canvasSize.height}
                className="absolute"
                style={{ 
                  left: canvasOffset.x,
                  top: canvasOffset.y,
                  cursor: draggingBlock ? 'grabbing' : (isPanning ? 'grabbing' : 'default')
                }}
                onMouseMove={handleMouseMove}
                onMouseUp={handleMouseUp}
                onClick={cancelConnection}
              >
                {/* Arrow marker definition */}
                <defs>
                  <marker
                    id="arrowhead"
                    markerWidth="20"
                    markerHeight="20"
                    refX="18"
                    refY="10"
                    orient="auto"
                    markerUnits="userSpaceOnUse"
                    viewBox="0 0 20 20"
                  >
                    <polygon 
                      points="0 0, 20 10, 0 20" 
                      fill="#2563eb"
                      stroke="#2563eb"
                      strokeWidth="1"
                    />
                  </marker>
                </defs>

                {/* Draw all connections */}
                {connections.map((connection, index) => drawConnection(connection, index))}

                {/* Draw temporary connection line when connecting */}
                {connectingFrom && hoveredBlock && (() => {
                  const tempConnectionPoints = getConnectionPoint(connectingFrom, hoveredBlock);
                  return (
                    <line
                      x1={tempConnectionPoints.from.x}
                      y1={tempConnectionPoints.from.y}
                      x2={tempConnectionPoints.to.x}
                      y2={tempConnectionPoints.to.y}
                      stroke="#9ca3af"
                      strokeWidth="2"
                      strokeDasharray="5,5"
                      markerEnd="url(#arrowhead)"
                    />
                  );
                })()}

                {/* Draw all blocks */}
                {blocks.map(block => (
                  <g key={block.id}>
                    {block.type === BLOCK_TYPES.EVENT ? (
                      // Event - Oval shape
                      <ellipse
                        cx={block.x + block.width / 2}
                        cy={block.y + block.height / 2}
                        rx={block.width / 2}
                        ry={block.height / 2}
                        fill={selectedBlock?.id === block.id ? '#a78bfa' : '#c4b5fd'}
                        stroke={connectingFrom?.id === block.id ? '#f59e0b' : '#7c3aed'}
                        strokeWidth={connectingFrom?.id === block.id ? '3' : '2'}
                        onMouseDown={(e) => handleBlockMouseDown(e, block)}
                        onContextMenu={(e) => {
                          e.preventDefault();
                          startConnection(e, block);
                        }}
                        onClick={(e) => {
                          e.stopPropagation();
                          if (connectingFrom) {
                            completeConnection(e, block);
                          }
                        }}
                        onDoubleClick={(e) => {
                          e.stopPropagation();
                          startEditingBlock(block);
                        }}
                        onMouseEnter={() => setHoveredBlock(block)}
                        onMouseLeave={() => setHoveredBlock(null)}
                        style={{ cursor: draggingBlock?.id === block.id ? 'grabbing' : 'grab' }}
                      />
                    ) : (
                      // Command - Rectangle shape
                      <rect
                        x={block.x}
                        y={block.y}
                        width={block.width}
                        height={block.height}
                        fill={selectedBlock?.id === block.id ? '#60a5fa' : '#93c5fd'}
                        stroke={connectingFrom?.id === block.id ? '#f59e0b' : '#2563eb'}
                        strokeWidth={connectingFrom?.id === block.id ? '3' : '2'}
                        rx="5"
                        onMouseDown={(e) => handleBlockMouseDown(e, block)}
                        onContextMenu={(e) => {
                          e.preventDefault();
                          startConnection(e, block);
                        }}
                        onClick={(e) => {
                          e.stopPropagation();
                          if (connectingFrom) {
                            completeConnection(e, block);
                          }
                        }}
                        onDoubleClick={(e) => {
                          e.stopPropagation();
                          startEditingBlock(block);
                        }}
                        onMouseEnter={() => setHoveredBlock(block)}
                        onMouseLeave={() => setHoveredBlock(null)}
                        style={{ cursor: draggingBlock?.id === block.id ? 'grabbing' : 'grab' }}
                      />
                    )}
                    {/* Block label */}
                    <text
                      x={block.x + block.width / 2}
                      y={block.y + block.height / 2 + 5}
                      textAnchor="middle"
                      fontSize="14"
                      fontWeight="500"
                      fill="#1f2937"
                      pointerEvents="none"
                    >
                      {block.name}
                    </text>
                    
                    {/* Description icon - small circle in top-left corner */}
                    <g>
                      <circle
                        cx={block.x + 15}
                        cy={block.y + 15}
                        r="8"
                        fill="#f59e0b"
                        stroke="#ffffff"
                        strokeWidth="2"
                        onClick={(e) => {
                          e.stopPropagation();
                          startEditingDescription(block);
                        }}
                        style={{ cursor: 'pointer' }}
                      />
                      <text
                        x={block.x + 15}
                        y={block.y + 18}
                        textAnchor="middle"
                        fontSize="10"
                        fontWeight="bold"
                        fill="#ffffff"
                        pointerEvents="none"
                      >
                        📝
                      </text>
                    </g>
                    
                    {/* Code icon - small circle in top-right corner */}
                    <g>
                      <circle
                        cx={block.x + block.width - 15}
                        cy={block.y + 15}
                        r="8"
                        fill="#3b82f6"
                        stroke="#ffffff"
                        strokeWidth="2"
                        onClick={(e) => {
                          e.stopPropagation();
                          startEditingCode(block);
                        }}
                        style={{ cursor: 'pointer' }}
                      />
                      <text
                        x={block.x + block.width - 15}
                        y={block.y + 18}
                        textAnchor="middle"
                        fontSize="10"
                        fontWeight="bold"
                        fill="#ffffff"
                        pointerEvents="none"
                      >
                        💻
                      </text>
                    </g>
                    
                    {/* Connection handle - small circle on the right side for mobile/desktop */}
                    <g>
                      <circle
                        cx={block.x + block.width}
                        cy={block.y + block.height / 2}
                        r="10"
                        fill={connectingFrom?.id === block.id ? '#f59e0b' : '#10b981'}
                        stroke="#ffffff"
                        strokeWidth="2"
                        onClick={(e) => handleConnectionButtonClick(e, block)}
                        onTouchStart={(e) => handleConnectionButtonClick(e, block)}
                        style={{ cursor: 'pointer' }}
                      />
                      {/* Plus icon or link icon inside the circle */}
                      <path
                        d={`M ${block.x + block.width - 4} ${block.y + block.height / 2} L ${block.x + block.width + 4} ${block.y + block.height / 2} M ${block.x + block.width} ${block.y + block.height / 2 - 4} L ${block.x + block.width} ${block.y + block.height / 2 + 4}`}
                        stroke="#ffffff"
                        strokeWidth="2"
                        strokeLinecap="round"
                        pointerEvents="none"
                      />
                    </g>
                  </g>
                ))}
              </svg>
            </div>

            {/* Status bar */}
            <div className="mt-4 p-3 bg-blue-50 border border-blue-200 rounded-md flex justify-between items-center">
              <div className="flex items-center space-x-4">
                <span className="text-sm text-blue-800">
                  <strong>Canvas:</strong> {canvasSize.width} × {canvasSize.height}px
                </span>
                <span className="text-sm text-blue-800">
                  <strong>Blocks:</strong> {blocks.length}
                </span>
                <span className="text-sm text-blue-800">
                  <strong>Connections:</strong> {connections.length}
                </span>
                {isPanning && (
                  <span className="text-sm text-orange-800 font-medium">
                    🖱️ Panning mode - Drag to move canvas
                  </span>
                )}
              </div>
              <div className="text-xs text-blue-600">
                💡 Drag empty space to pan • Right-click blocks to connect
              </div>
            </div>

            {connectingFrom && (
              <div className="mt-2 p-3 bg-yellow-50 border border-yellow-200 rounded-md">
                <p className="text-sm text-yellow-800">
                  <strong>Connecting from "{connectingFrom.name}"</strong> - Click/tap on another block to complete the connection, or click the green circle again / press Escape to cancel.
                </p>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Edit Block Name Modal */}
      {editingBlock && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                Edit Block Name
              </h3>
              <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Block Name
                </label>
                <input
                  type="text"
                  value={blockNameInput}
                  onChange={(e) => setBlockNameInput(e.target.value)}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      saveBlockName();
                    }
                  }}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  autoFocus
                />
              </div>
              <div className="flex justify-end space-x-3">
                <button
                  type="button"
                  onClick={() => {
                    setEditingBlock(null);
                    setBlockNameInput('');
                  }}
                  className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                >
                  Cancel
                </button>
                <button
                  type="button"
                  onClick={saveBlockName}
                  className="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
                >
                  Save
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Edit Connection Name Modal */}
      {editingConnection && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
          <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
            <div className="mt-3">
              <h3 className="text-lg font-medium text-gray-900 mb-4">
                Edit Connection
              </h3>
              <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Connection Name (optional)
                </label>
                <input
                  type="text"
                  value={connectionNameInput}
                  onChange={(e) => setConnectionNameInput(e.target.value)}
                  onKeyPress={(e) => {
                    if (e.key === 'Enter') {
                      saveConnectionName();
                    }
                  }}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Enter connection label"
                  autoFocus
                />
              </div>
              <div className="flex justify-between">
                <button
                  type="button"
                  onClick={() => deleteConnection(editingConnection.id)}
                  className="px-4 py-2 text-sm font-medium text-white bg-red-600 hover:bg-red-700 rounded-md transition-colors"
                >
                  Delete Connection
                </button>
                <div className="flex space-x-3">
                  <button
                    type="button"
                    onClick={() => {
                      setEditingConnection(null);
                      setConnectionNameInput('');
                    }}
                    className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                  >
                    Cancel
                  </button>
                  <button
                    type="button"
                    onClick={saveConnectionName}
                    className="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
                  >
                    Save
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Clear All Confirmation Modal */}
      {showClearConfirm && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50 flex items-center justify-center" onClick={() => setShowClearConfirm(false)}>
          <div className="relative p-6 border w-full max-w-md shadow-lg rounded-md bg-white" onClick={(e) => e.stopPropagation()}>
            <div className="text-center">
              {/* Warning Icon */}
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-red-100 mb-4">
                <svg className="h-6 w-6 text-red-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-1.732-1.333-2.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                </svg>
              </div>
              
              <h3 className="text-lg font-bold text-gray-900 mb-2">
                Clear All Blocks and Connections?
              </h3>
              
              <p className="text-sm text-gray-600 mb-6">
                This will permanently delete all blocks and connections from the canvas. This action cannot be undone.
              </p>
              
              <div className="flex justify-center space-x-3">
                <button
                  onClick={() => setShowClearConfirm(false)}
                  className="px-6 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={confirmClearAll}
                  className="px-6 py-2 text-sm font-medium text-white bg-red-600 hover:bg-red-700 rounded-md transition-colors"
                >
                  Clear All
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Description Editing Modal */}
      {editingDescription && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50 flex items-center justify-center" onClick={() => {
          setEditingDescription(null);
          setDescriptionInput('');
        }}>
          <div className="relative p-6 border w-full max-w-lg shadow-lg rounded-md bg-white" onClick={(e) => e.stopPropagation()}>
            <div className="text-center mb-4">
              {/* Description Icon */}
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-yellow-100 mb-4">
                <svg className="h-6 w-6 text-yellow-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                </svg>
              </div>
              
              <h3 className="text-lg font-bold text-gray-900 mb-2">
                Edit Description for "{editingDescription.name}"
              </h3>
            </div>
            
            <textarea
              value={descriptionInput}
              onChange={(e) => setDescriptionInput(e.target.value)}
              placeholder="Enter a detailed description for this block..."
              className="w-full h-32 p-3 border border-gray-300 rounded-md resize-none focus:outline-none focus:ring-2 focus:ring-yellow-500 focus:border-transparent"
              autoFocus
            />
            
            <div className="flex justify-center space-x-3 mt-4">
              <button
                onClick={() => {
                  setEditingDescription(null);
                  setDescriptionInput('');
                }}
                className="px-6 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={saveDescription}
                className="px-6 py-2 text-sm font-medium text-white bg-yellow-600 hover:bg-yellow-700 rounded-md transition-colors"
              >
                Save Description
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Code Editing Modal */}
      {editingCode && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50 flex items-center justify-center" onClick={() => {
          setEditingCode(null);
          setCodeInput('');
        }}>
          <div className="relative p-6 border w-full max-w-2xl shadow-lg rounded-md bg-white" onClick={(e) => e.stopPropagation()}>
            <div className="text-center mb-4">
              {/* Code Icon */}
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-blue-100 mb-4">
                <svg className="h-6 w-6 text-blue-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M10 20l4-16m4 4l4 4-4 4M6 16l-4-4 4-4" />
                </svg>
              </div>
              
              <h3 className="text-lg font-bold text-gray-900 mb-2">
                Edit Code for "{editingCode.name}"
              </h3>
            </div>
            
            <textarea
              value={codeInput}
              onChange={(e) => setCodeInput(e.target.value)}
              placeholder="Enter code, pseudocode, or implementation details..."
              className="w-full h-64 p-3 border border-gray-300 rounded-md resize-none focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent font-mono text-sm"
              autoFocus
            />
            
            <div className="flex justify-center space-x-3 mt-4">
              <button
                onClick={() => {
                  setEditingCode(null);
                  setCodeInput('');
                }}
                className="px-6 py-2 text-sm font-medium text-gray-700 bg-gray-200 hover:bg-gray-300 rounded-md transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={saveCode}
                className="px-6 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
              >
                Save Code
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Instructions Modal */}
      {showInstructions && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50" onClick={() => setShowInstructions(false)}>
          <div className="relative top-20 mx-auto p-6 border w-full max-w-2xl shadow-lg rounded-md bg-white" onClick={(e) => e.stopPropagation()}>
            <div className="flex justify-between items-start mb-4">
              <div className="flex items-center space-x-2">
                <svg className="w-6 h-6 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-8-3a1 1 0 00-.867.5 1 1 0 11-1.731-1A3 3 0 0113 8a3.001 3.001 0 01-2 2.83V11a1 1 0 11-2 0v-1a1 1 0 011-1 1 1 0 100-2zm0 8a1 1 0 100-2 1 1 0 000 2z" clipRule="evenodd" />
                </svg>
                <h3 className="text-xl font-bold text-gray-900">How to Use State Machine</h3>
              </div>
              <button
                onClick={() => setShowInstructions(false)}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                </svg>
              </button>
            </div>

            <div className="space-y-6">
              {/* Block Management */}
              <div>
                <h4 className="text-lg font-semibold text-gray-800 mb-3 flex items-center">
                  <span className="bg-blue-100 text-blue-800 rounded-full w-6 h-6 flex items-center justify-center text-sm mr-2">1</span>
                  Adding and Moving Blocks
                </h4>
                <ul className="space-y-2 ml-8 text-gray-700">
                  <li className="flex items-start">
                    <span className="text-purple-600 mr-2">•</span>
                    <span>Click <strong>"Add Event (Oval)"</strong> to create an event block (purple oval)</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-blue-600 mr-2">•</span>
                    <span>Click <strong>"Add Command (Rectangle)"</strong> to create a command block (blue rectangle)</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-gray-600 mr-2">•</span>
                    <span><strong>Drag blocks</strong> around the canvas to position them</span>
                  </li>
                </ul>
              </div>

              {/* Editing Blocks */}
              <div>
                <h4 className="text-lg font-semibold text-gray-800 mb-3 flex items-center">
                  <span className="bg-blue-100 text-blue-800 rounded-full w-6 h-6 flex items-center justify-center text-sm mr-2">2</span>
                  Editing Block Names
                </h4>
                <ul className="space-y-2 ml-8 text-gray-700">
                  <li className="flex items-start">
                    <span className="text-green-600 mr-2">•</span>
                    <span><strong>Double-click</strong> or <strong>double-tap</strong> any block to edit its name</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-green-600 mr-2">•</span>
                    <span>Press <strong>Enter</strong> to save or <strong>Escape</strong> to cancel</span>
                  </li>
                </ul>
              </div>

              {/* Creating Connections */}
              <div>
                <h4 className="text-lg font-semibold text-gray-800 mb-3 flex items-center">
                  <span className="bg-blue-100 text-blue-800 rounded-full w-6 h-6 flex items-center justify-center text-sm mr-2">3</span>
                  Connecting Blocks
                </h4>
                <ul className="space-y-2 ml-8 text-gray-700">
                  <li className="flex items-start">
                    <span className="text-green-600 mr-2">⊕</span>
                    <span><strong>Click/tap the green circle (⊕)</strong> on the right side of a block to start a connection</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-orange-600 mr-2">•</span>
                    <span>The circle turns <strong>orange</strong> when in connection mode</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-blue-600 mr-2">•</span>
                    <span><strong>Click/tap on another block</strong> to complete the connection</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-purple-600 mr-2">→</span>
                    <span><strong>Directional:</strong> Connections have arrows showing direction. A→B is different from B→A</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-indigo-600 mr-2">⇉</span>
                    <span><strong>Multiple connections:</strong> You can create multiple connections from A to B with different names</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-gray-600 mr-2">•</span>
                    <span><strong>Desktop only:</strong> You can also right-click a block to start a connection</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-red-600 mr-2">•</span>
                    <span>To cancel: Click the orange circle again or press <strong>Escape</strong></span>
                  </li>
                </ul>
              </div>

              {/* Editing Connections */}
              <div>
                <h4 className="text-lg font-semibold text-gray-800 mb-3 flex items-center">
                  <span className="bg-blue-100 text-blue-800 rounded-full w-6 h-6 flex items-center justify-center text-sm mr-2">4</span>
                  Managing Connections
                </h4>
                <ul className="space-y-2 ml-8 text-gray-700">
                  <li className="flex items-start">
                    <span className="text-blue-600 mr-2">•</span>
                    <span><strong>Click/tap on a connection line</strong> to add or edit its label</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-orange-600 mr-2">•</span>
                    <span><strong>Double-click on a connection line</strong> to add a control point for custom shaping</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-yellow-600 mr-2">•</span>
                    <span><strong>Drag the orange control points</strong> to modify the connection shape</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-red-600 mr-2">•</span>
                    <span>Use the modal to <strong>delete a connection</strong></span>
                  </li>
                </ul>
              </div>

              {/* Deleting Blocks */}
              <div>
                <h4 className="text-lg font-semibold text-gray-800 mb-3 flex items-center">
                  <span className="bg-blue-100 text-blue-800 rounded-full w-6 h-6 flex items-center justify-center text-sm mr-2">5</span>
                  Deleting Blocks
                </h4>
                <ul className="space-y-2 ml-8 text-gray-700">
                  <li className="flex items-start">
                    <span className="text-red-600 mr-2">•</span>
                    <span>Click a block to select it, then press the <strong>Delete</strong> key</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-red-600 mr-2">•</span>
                    <span>Or click the <strong>"Delete Selected"</strong> button in the toolbar</span>
                  </li>
                  <li className="flex items-start">
                    <span className="text-gray-600 mr-2">•</span>
                    <span>Deleting a block also removes all its connections</span>
                  </li>
                </ul>
              </div>

              {/* Auto-Save Feature */}
              <div className="bg-green-50 border border-green-200 p-4 rounded-md">
                <h4 className="text-sm font-semibold text-green-800 mb-2 flex items-center">
                  <svg className="w-4 h-4 mr-2" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  Auto-Save Enabled
                </h4>
                <p className="text-sm text-green-700">
                  Your work is automatically saved to your browser's local storage. When you reload the page, all your blocks and connections will be restored exactly as you left them.
                </p>
                <p className="text-sm text-green-700 mt-2">
                  To start fresh, click the <strong>"Clear All"</strong> button in the toolbar.
                </p>
              </div>

              {/* Keyboard Shortcuts */}
              <div className="bg-gray-50 p-4 rounded-md">
                <h4 className="text-sm font-semibold text-gray-800 mb-2">⌨️ Keyboard Shortcuts</h4>
                <div className="grid grid-cols-2 gap-2 text-sm text-gray-700">
                  <div><kbd className="px-2 py-1 bg-white border rounded">Delete</kbd> - Remove selected block</div>
                  <div><kbd className="px-2 py-1 bg-white border rounded">Escape</kbd> - Cancel action</div>
                  <div><kbd className="px-2 py-1 bg-white border rounded">Enter</kbd> - Save in modals</div>
                  <div><kbd className="px-2 py-1 bg-white border rounded">Double-click</kbd> - Edit block name</div>
                </div>
              </div>
            </div>

            <div className="mt-6 flex justify-end">
              <button
                onClick={() => setShowInstructions(false)}
                className="px-6 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
              >
                Got it!
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default StateMachine;

