import OIDC_CONFIG from '../config/oidc';

// Simple PKCE + OIDC client without external deps

const STORAGE_KEYS = {
  codeVerifier: 'oidc_code_verifier',
  state: 'oidc_state',
  oidcMeta: 'oidc_meta',
};

async function sha256(input) {
  const data = new TextEncoder().encode(input);
  const hash = await crypto.subtle.digest('SHA-256', data);
  return new Uint8Array(hash);
}

function base64UrlEncode(buffer) {
  let str = btoa(String.fromCharCode.apply(null, Array.from(buffer)));
  return str.replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
}

function randomString(length) {
  const array = new Uint8Array(length);
  crypto.getRandomValues(array);
  return Array.from(array, (b) => ('0' + b.toString(16)).slice(-2)).join('');
}

async function generatePkce() {
  const codeVerifier = base64UrlEncode(await sha256(randomString(64)));
  const codeChallenge = base64UrlEncode(await sha256(codeVerifier));
  return { codeVerifier, codeChallenge };
}

async function fetchDiscovery() {
  const res = await fetch(OIDC_CONFIG.discoveryUrl);
  if (!res.ok) throw new Error('Failed to fetch OIDC discovery');
  return res.json();
}

export async function startLogin(returnTo = '/') {
  const discovery = await fetchDiscovery();
  const { codeVerifier, codeChallenge } = await generatePkce();
  const state = base64UrlEncode(await sha256(randomString(32)));

  localStorage.setItem(STORAGE_KEYS.codeVerifier, codeVerifier);
  localStorage.setItem(STORAGE_KEYS.state, state);
  localStorage.setItem(
    STORAGE_KEYS.oidcMeta,
    JSON.stringify({ returnTo })
  );

  const authUrl = new URL(discovery.authorization_endpoint);
  authUrl.searchParams.set('response_type', 'code');
  authUrl.searchParams.set('client_id', OIDC_CONFIG.clientId);
  authUrl.searchParams.set('redirect_uri', OIDC_CONFIG.redirectUri);
  authUrl.searchParams.set('scope', OIDC_CONFIG.scopes.join(' '));
  authUrl.searchParams.set('code_challenge', codeChallenge);
  authUrl.searchParams.set('code_challenge_method', 'S256');
  authUrl.searchParams.set('state', state);

  window.location.assign(authUrl.toString());
}

export async function handleCallback() {
  const discovery = await fetchDiscovery();
  const params = new URLSearchParams(window.location.search);
  const code = params.get('code');
  const returnedState = params.get('state');
  const expectedState = localStorage.getItem(STORAGE_KEYS.state);
  const codeVerifier = localStorage.getItem(STORAGE_KEYS.codeVerifier);

  if (!code) throw new Error('Missing authorization code');
  if (!returnedState || returnedState !== expectedState) throw new Error('Invalid state');
  if (!codeVerifier) throw new Error('Missing code verifier');

  // Exchange code for tokens
  const body = new URLSearchParams();
  body.set('grant_type', 'authorization_code');
  body.set('code', code);
  body.set('redirect_uri', OIDC_CONFIG.redirectUri);
  body.set('client_id', OIDC_CONFIG.clientId);
  body.set('code_verifier', codeVerifier);

  const tokenRes = await fetch(discovery.token_endpoint, {
    method: 'POST',
    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
    body,
  });
  if (!tokenRes.ok) throw new Error('Token exchange failed');
  const tokens = await tokenRes.json();

  // Fetch userinfo
  const userRes = await fetch(discovery.userinfo_endpoint, {
    headers: { Authorization: `Bearer ${tokens.access_token}` },
  });
  if (!userRes.ok) throw new Error('Failed to fetch userinfo');
  const user = await userRes.json();

  // Persist
  localStorage.setItem('token', tokens.access_token);
  if (tokens.refresh_token) localStorage.setItem('refresh_token', tokens.refresh_token);
  localStorage.setItem('user', JSON.stringify(user));

  // Cleanup
  localStorage.removeItem(STORAGE_KEYS.codeVerifier);
  localStorage.removeItem(STORAGE_KEYS.state);

  const meta = JSON.parse(localStorage.getItem(STORAGE_KEYS.oidcMeta) || '{}');
  localStorage.removeItem(STORAGE_KEYS.oidcMeta);
  return { user, tokens, returnTo: meta.returnTo || '/dashboard' };
}

export async function logoutOidc() {
  // Kanidm supports revocation endpoint per discovery metadata
  try {
    const discovery = await fetchDiscovery();
    const refreshToken = localStorage.getItem('refresh_token');
    if (refreshToken) {
      const body = new URLSearchParams();
      body.set('token', refreshToken);
      body.set('token_type_hint', 'refresh_token');
      body.set('client_id', OIDC_CONFIG.clientId);
      await fetch(discovery.revocation_endpoint, {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body,
      });
    }
  } catch (e) {
    // ignore revocation errors
  } finally {
    localStorage.removeItem('token');
    localStorage.removeItem('refresh_token');
    localStorage.removeItem('user');
  }
}

export async function refreshTokens() {
  const discovery = await fetchDiscovery();
  const refreshToken = localStorage.getItem('refresh_token');
  if (!refreshToken) throw new Error('No refresh token');

  const body = new URLSearchParams();
  body.set('grant_type', 'refresh_token');
  body.set('refresh_token', refreshToken);
  body.set('client_id', OIDC_CONFIG.clientId);

  const tokenRes = await fetch(discovery.token_endpoint, {
    method: 'POST',
    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
    body,
  });
  if (!tokenRes.ok) throw new Error('Refresh token exchange failed');
  const tokens = await tokenRes.json();

  localStorage.setItem('token', tokens.access_token);
  if (tokens.refresh_token) localStorage.setItem('refresh_token', tokens.refresh_token);

  return tokens;
}

const oidcService = {
  startLogin,
  handleCallback,
  logoutOidc,
  refreshTokens,
};

export default oidcService;



